    .services-compare-grid {
        grid-template-columns: 1fr;
    }
}

@media (max-width: 968px) {
    .services-fullpage {
        height: auto;
        min-height: calc(100vh - 146px);
    }

    .services-container {
        padding: 30px 20px;
    }

    .services-grid-compact {
        grid-template-columns: repeat(2, 1fr);
        gap: 14px;
    }

    .services-steps {
        grid-template-columns: 1fr;
    }

    .service-card-compact {
        padding: 16px 12px;
    }

    .service-icon-compact {
        font-size: 2rem;
    }
}

@media (max-width: 768px) {
    .services-container {
        padding: 25px 16px;
        gap: 18px;
    }

    .services-main-title {
        font-size: 1.8rem;
    }

    .services-main-subtitle {
        font-size: 0.9rem;
    }

    .services-grid-compact {
        grid-template-columns: 1fr;
        gap: 12px;
    }

    .services-info-grid {
        grid-template-columns: 1fr;
    }

    .services-claims-grid {
        grid-template-columns: 1fr;
    }

    .services-compare-table {
        min-width: 520px;
    }

    .service-card-compact {
        padding: 16px 14px;
        gap: 8px;
    }

    .service-icon-compact {
        font-size: 1.8rem;
    }

    .service-features-compact {
        gap: 5px;
    }
}

.services-panel--compare .services-compare-grid {
    cursor: grab;
}

.services-panel--compare .services-compare-grid.is-dragging {
    cursor: grabbing;
    user-select: none;
}

.services-compare-table-wrapper {
    cursor: grab;
}

