    margin: 0 0 4px 0;
    color: #333;
    font-size: 16px;
    display: flex;
    align-items: center;
    gap: 8px;
    flex-wrap: wrap;
}

.client-info p {
    margin: 0 0 8px 0;
    color: #666;
    font-size: 14px;
}

.client-meta {
    display: flex;
    gap: 12px;
    font-size: 13px;
    color: #999;
    flex-wrap: wrap;
}

.client-meta .text-muted {
    color: #999;
    font-style: italic;
}

