@media (max-width: 1400px) {
    .about-content-grid {
        gap: 1.5vw;
    }
}

@media (max-width: 1200px) {
    .about-content-grid {
        --about-core-panel-w: clamp(300px, 80vw, 520px);
        gap: 14px;
    }

    .about-stats-strip {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 968px) {
    .about-fullpage {
        height: auto;
        min-height: calc(100vh - 146px);
    }

    .about-container {
        padding: 25px 20px;
    }

    .about-content-grid {
        --about-core-panel-w: clamp(300px, 88vw, 560px);
        gap: 12px;
    }

    .about-team-grid {
        grid-template-columns: repeat(3, 1fr);
        gap: 0.8rem;
    }
}

@media (max-width: 768px) {
    .about-container {
        padding: 20px 16px;
        gap: 2vh;
    }

    .about-main-title {
        font-size: 1.8rem;
    }

    .about-main-subtitle {
        font-size: 0.9rem;
    }

    .about-story-card,
    .about-mission-card,
    .about-team-card {
        padding: 1.2rem;
        gap: 0.8rem;
    }

    .about-content-grid {
        --about-core-panel-w: 92vw;
    }

    .team-grid-mini {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 560px) {
    .about-content-grid {
        --about-core-panel-w: 92vw;
    }

    .about-stats-strip {
        grid-template-columns: 1fr;
    }
}

@media (prefers-reduced-motion: reduce) {
    .about-partners-track {
        animation: none;
    }
