  top: 50%;
  transform: translateY(-50%);
  color: rgba(255, 255, 255, 0.25);
  font-weight: 300;
  font-size: 18px;
  pointer-events: none;
  width: 1px;
  height: 100%;
}

.sub-navigation.show {
