}

@keyframes subNavSlideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Navbar */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: var(--nav-height);
  z-index: 9999;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  border-bottom: 1px solid rgba(0, 0, 0, 0.06);
  padding: 0;
  margin: 0;
  transition: all 0.3s ease;
  box-shadow: var(--shadow-sm);
  display: flex;
  align-items: center;
}

.navbar.scrolled {
  box-shadow: var(--shadow);
}

.nav-container {
  max-width: 100%;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 0 48px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
}

.nav-brand {
  display: flex;
  align-items: center;
  gap: 12px;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.nav-brand .krause-shield {
  width: var(--logo-size-sm);
  height: var(--logo-size-sm);
}

.nav-brand:hover {
  transform: translateY(-2px);
}


.logo-shield {
  width: var(--logo-size-md);
  height: var(--logo-size-md);
  max-width: calc(var(--logo-size-lg) - 16px);
  max-height: calc(var(--logo-size-lg) - 16px);
  color: inherit;
  filter: drop-shadow(0 2px 4px var(--theme-primary-shadow-color));
}

/* Logo general - SIN auto-animacin, solo estilos base */
.krause-shield .shield-circle,
.krause-shield .shield-diamond,
.krause-shield .shield-arc,
.krause-shield .shield-line {
  fill: none;
  stroke: currentColor;
  stroke-width: 3;
  /* NO auto-animar - dejar que sean estticos o controlados por JS */
}

/* Hover solo cuando NO est en loading */
body:not(.loading-locked) .krause-shield:hover .shield-circle,
body:not(.loading-locked) .krause-shield:hover .shield-diamond,
body:not(.loading-locked) .krause-shield:hover .shield-arc,
body:not(.loading-locked) .krause-shield:hover .shield-line {
  animation: logoPulse 0.6s ease-in-out;
}

@keyframes logoPulse {

  0%,
  100% {
    transform: scale(1);
    opacity: 1;
  }

  50% {
    transform: scale(1.05);
    opacity: 0.8;
  }
}

.brand-text {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.brand-name {
  font-family: 'Cinzel', serif;
  font-size: 22px;
  font-weight: 600;
  color: var(--brand-maroon);
  line-height: 1;
}

.brand-tagline {
  font-size: 11px;
  color: var(--text-light);
  font-weight: 500;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

.mobile-menu-toggle {
  display: none;
  flex-direction: column;
  gap: 5px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 8px;
}

.mobile-menu-toggle span {
  width: 25px;
  height: 3px;
  background: var(--brand-maroon);
  border-radius: 2px;
  transition: all 0.3s ease;
}

.nav-menu {
  display: flex;
  align-items: center;
  gap: 12px;
}

.nav-link {
  padding: 10px 20px;
  color: var(--text-dark);
  text-decoration: none;
  font-weight: 500;
  transition: all 0.3s ease;
  border-radius: 8px;
  font-size: 16px;
}

.nav-link:hover,
.nav-link.active {
  color: var(--brand-maroon);
  background: var(--theme-navlink-active-bg);
  backdrop-filter: blur(15px) saturate(150%);
  -webkit-backdrop-filter: blur(15px) saturate(150%);
}

.nav-buttons {
  display: flex;
  gap: 12px;
  margin-left: 20px;
}

/* Generic buttons (used in Home CTA + Auth forms) */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  padding: 12px 18px;
  border-radius: 12px;
  border: 1px solid transparent;
  font-family: inherit;
  font-weight: 700;
  font-size: 0.95rem;
  line-height: 1;
  cursor: pointer;
  user-select: none;
  text-decoration: none;
  background: transparent;
  color: inherit;
  transition: transform 0.15s ease, box-shadow 0.25s ease, background 0.25s ease,
    border-color 0.25s ease, color 0.25s ease, filter 0.25s ease;
}

.btn:hover {
  transform: translateY(-1px);
}

.btn:active {
  transform: translateY(0);
}

.btn:disabled,
.btn[disabled] {
  opacity: 0.65;
  cursor: not-allowed;
  transform: none;
}

.btn:focus-visible {
  outline: 2px solid var(--theme-highlight-light);
  outline-offset: 2px;
}

.btn-lg {
  padding: 14px 22px;
  border-radius: 14px;
  font-size: 1rem;
}

.btn-block {
  width: 100%;
}

.btn-primary {
  background: radial-gradient(circle at 35% 35%, var(--theme-primary-color) 0%, var(--theme-secondary-color) 70%);
  color: var(--white);
  border-color: rgba(255, 255, 255, 0.16);
  box-shadow: 0 10px 24px rgba(0, 0, 0, 0.22), 0 10px 24px var(--theme-primary-shadow-color);
}

.btn.is-loading,
.btn[aria-busy="true"] {
  position: relative;
  pointer-events: none;
  color: var(--theme-contrast-strong, var(--brand-maroon));
  border-color: var(--theme-contrast-glow, rgba(255, 255, 255, 0.25));
  filter: brightness(1.02);
}

.btn.is-loading::after,
.btn[aria-busy="true"]::after {
  content: '';
  position: absolute;
  inset: 6px;
  border-radius: inherit;
  background: radial-gradient(circle at 40% 40%, rgba(255, 255, 255, 0.35), rgba(255, 255, 255, 0));
  animation: loading-pulse 0.9s ease-in-out infinite;
  mix-blend-mode: screen;
}

@keyframes loading-pulse {

  0%,
  100% {
    opacity: 0.35;
    transform: scale(0.98);
  }

  50% {
    opacity: 0.75;
    transform: scale(1.02);
  }
}

.btn-primary:hover {
  filter: brightness(1.05);
  box-shadow: 0 14px 34px rgba(0, 0, 0, 0.26), 0 14px 34px var(--theme-primary-shadow-color);
}

.btn-secondary {
  background: rgba(255, 255, 255, 0.14);
  color: white;
  border-color: rgba(255, 255, 255, 0.20);
  box-shadow: 0 10px 24px rgba(0, 0, 0, 0.18);
}

.btn-secondary:hover {
  background: rgba(255, 255, 255, 0.18);
  border-color: rgba(255, 255, 255, 0.26);
  box-shadow: 0 14px 34px rgba(0, 0, 0, 0.22);
}

.btn-outline {
  background: transparent;
  color: var(--theme-primary-color);
  border-color: rgba(255, 255, 255, 0.35);
}

.btn-outline:hover {
  background: rgba(255, 255, 255, 0.12);
  color: var(--theme-secondary-color);
}

.btn-nav {
  padding: 12px 24px;
  border-radius: 8px;
  font-weight: 600;
  font-size: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
  font-family: inherit;
}

.btn-login {
  background: transparent;
  color: var(--brand-maroon);
  border: 2px solid var(--brand-maroon);
}

.btn-login:hover {
  background: var(--brand-maroon);
  color: white;
}

.btn-agent {
  background: var(--brand-maroon);
  color: white;
  border: 2px solid var(--brand-maroon);
}

.btn-agent:hover {
  background: var(--wine);
  border-color: var(--wine);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px var(--theme-primary-shadow-color);
}

/* Main Content */
.main-content {
  position: fixed;
  top: var(--nav-height);
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  height: auto;
  width: 100%;
  box-sizing: border-box;
  overflow-y: auto;
  overflow-x: hidden;
}

body:not(.no-footer) .main-content {
  padding-bottom: var(--footer-height);
}

body.no-footer .main-content {
  padding-bottom: 0;
}

body.no-footer .footer {
  display: none !important;
}

/* Home has a fixed sub-navigation that overlays the scroll container.
   Add top padding only on home so sections (esp. Cotiza logo/title) are not cut. */
body[data-page="home"] .main-content {
  padding-top: var(--subnav-height);
}

/* Dashboards sin navbar: usar todo el alto disponible sin offset del nav */
body[data-page="client-dashboard"] .main-content,
body[data-page="agent-dashboard"] .main-content,
body[data-page="admin-dashboard"] .main-content {
  top: 0;
  padding-top: 0;
}



/* Sub Navigation */
.sub-navigation {
  position: fixed;
  top: var(--nav-height);
  left: 0;
  right: 0;
  width: 100%;
  z-index: 9998;
  background: var(--acrylic-bg-dark);
  backdrop-filter: blur(25px) saturate(180%);
  -webkit-backdrop-filter: blur(25px) saturate(180%);
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
  height: var(--subnav-height);
  transition: all 0.3s ease;
}

.sub-nav-btn {
  position: relative;
  padding: 18px 32px;
  background: transparent;
  border: none;
  border-radius: 0 !important;
  font-size: 13px;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.85);
  cursor: pointer;
  will-change: transform, opacity;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  font-family: inherit;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  overflow: hidden;
  height: 56px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.sub-nav-btn::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 3px;
  background: linear-gradient(90deg, transparent, var(--theme-highlight-light), transparent);
  opacity: 0;
  transform: scaleX(0);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  pointer-events: none;
}

.sub-nav-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px) saturate(160%);
  -webkit-backdrop-filter: blur(20px) saturate(160%);
  color: white;
}

.sub-nav-btn:hover::before {
  opacity: 0.5;
  transform: scaleX(1);
}

.sub-nav-btn.active {
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(25px) saturate(170%);
  -webkit-backdrop-filter: blur(25px) saturate(170%);
  color: white;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.sub-nav-btn.active::before {
  opacity: 1;
  transform: scaleX(1);
  box-shadow: 0 0 15px var(--theme-highlight-glow);
}

/* Button separators */
.sub-nav-btn:not(:last-child)::after {
  content: '|';
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  color: rgba(255, 255, 255, 0.25);
  font-weight: 300;
  font-size: 18px;
  pointer-events: none;
  width: 1px;
  height: 100%;
}

