EACE,eAAe;EACf,sBAAsB;EACtB,OAAO;EACP,QAAQ;EACR,SAAS;EACT,WAAW;EACX,YAAY;EACZ,WAAW;EACX,sBAAsB;EACtB,gBAAgB;EAChB,kBAAkB;AACpB;;AAEA;EACE,oCAAoC;AACtC;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,wBAAwB;AAC1B;;AAEA;mFACmF;AACnF;EACE,iCAAiC;AACnC;;AAEA,2EAA2E;AAC3E;;;EAGE,MAAM;EACN,cAAc;AAChB;;;;AAIA,mBAAmB;AACnB;EACE,eAAe;EACf,sBAAsB;EACtB,OAAO;EACP,QAAQ;EACR,WAAW;EACX,aAAa;EACb,kCAAkC;EAClC,0CAA0C;EAC1C,kDAAkD;EAClD,kDAAkD;EAClD,UAAU;EACV,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,MAAM;EACN,yCAAyC;EACzC,4BAA4B;EAC5B,yBAAyB;AAC3B;;AAEA;EACE,kBAAkB;EAClB,kBAAkB;EAClB,uBAAuB;EACvB,YAAY;EACZ,2BAA2B;EAC3B,eAAe;EACf,gBAAgB;EAChB,gCAAgC;EAChC,eAAe;EACf,+BAA+B;EAC/B,iDAAiD;EACjD,oBAAoB;EACpB,yBAAyB;EACzB,qBAAqB;EACrB,gBAAgB;EAChB,YAAY;EACZ,aAAa;EACb,mBAAmB;EACnB,uBAAuB;AACzB;;AAEA;EACE,WAAW;EACX,kBAAkB;EAClB,SAAS;EACT,OAAO;EACP,QAAQ;EACR,WAAW;EACX,WAAW;EACX,0FAA0F;EAC1F,UAAU;EACV,oBAAoB;EACpB,iDAAiD;EACjD,oBAAoB;AACtB;;AAEA;EACE,oCAAoC;EACpC,0CAA0C;EAC1C,kDAAkD;EAClD,YAAY;AACd;;AAEA;EACE,YAAY;EACZ,oBAAoB;AACtB;;AAEA;EACE,qCAAqC;EACrC,0CAA0C;EAC1C,kDAAkD;EAClD,YAAY;EACZ,yCAAyC;EACzC,kDAAkD;AACpD;;AAEA;EACE,UAAU;EACV,oBAAoB;EACpB,gDAAgD;AAClD;;AAEA,sBAAsB;AACtB;EACE,YAAY;EACZ,kBAAkB;EAClB,QAAQ;EACR,QAAQ;EACR,2BAA2B;EAC3B,gCAAgC;EAChC,gBAAgB;EAChB,eAAe;EACf,oBAAoB;EACpB,UAAU;EACV,YAAY;AACd;;AAEA;;;;;;;;;;;CAWC","sourcesContent":["/* Krause Insurance App - Main Stylesheet */\r\n/* CSS now loaded via webpack imports in EntryPointMainApp.js */\r\n/* All imports removed - webpack handles module order */\r\n\r\n/* Global design tokens */\r\n:root {\r\n  /* Brand palette (shared by all logo renders) */\r\n  --brand-maroon: #8b2348;\r\n  --brand-purple: #9b59b6;\r\n  --wine: #722f37;\r\n  --baby-pink: #fef5f9;\r\n\r\n  --text-dark: #1a1a1a;\r\n  --text-light: #4a4f5c;\r\n  --white: #ffffff;\r\n  --gray-100: #f5f5f5;\r\n  --gray-200: #e0e0e0;\r\n  --gray-300: #ccc;\r\n  --success: #28a745;\r\n  --warning: #ffc107;\r\n  --danger: #dc3545;\r\n  --info: #17a2b8;\r\n  --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.08);\r\n  --shadow: 0 4px 16px rgba(0, 0, 0, 0.1);\r\n  --shadow-lg: 0 8px 32px rgba(0, 0, 0, 0.15);\r\n  --gradient-start: #631832;\r\n  --gradient-mid: #521a38;\r\n  --gradient-end: #401a59;\r\n  /* Unified logo sizing + contrast tokens */\r\n  --logo-size-sm: 56px;\r\n  --logo-size-md: 64px;\r\n  --logo-size-lg: 96px;\r\n  --logo-contrast-primary: var(--brand-maroon);\r\n  --logo-contrast-ink: #0f172a;\r\n  --logo-contrast-on-dark: #ffffff;\r\n  --logo-contrast-muted: var(--theme-text-secondary);\r\n  /* Layout measurements to keep padding off the global background */\r\n  --nav-height: 90px;\r\n  /* header */\r\n  --subnav-height: 56px;\r\n  /* secondary navigation */\r\n  --footer-height: 56px;\r\n  /* reserve space so content doesn't go under the collapsed footer */\r\n  /* Acrylic/Frosted Glass Effects - Default Theme */\r\n  --acrylic-blur: 20px;\r\n  --acrylic-blur-strong: 30px;\r\n  --acrylic-blur-subtle: 15px;\r\n  --acrylic-saturate: 170%;\r\n  --acrylic-saturate-strong: 180%;\r\n  --acrylic-saturate-subtle: 150%;\r\n  --acrylic-bg-light: rgba(255, 255, 255, 0.08);\r\n  --acrylic-bg-medium: rgba(255, 255, 255, 0.12);\r\n  --acrylic-bg-dark: rgba(99, 24, 50, 0.15);\r\n  --acrylic-border-light: rgba(255, 255, 255, 0.15);\r\n\r\n  /* Footer glass tokens (theme-overridable) */\r\n  /* Slightly stronger by default; themes can override for more/less glass. */\r\n  --footer-acrylic-bg: rgba(99, 24, 50, 0.7);\r\n  --footer-acrylic-blur: 32px;\r\n  --footer-acrylic-saturate: 200%;\r\n\r\n  /* Reusable semantic surfaces (so themes can flip light/dark cleanly) */\r\n  --theme-surface-bg: rgba(255, 255, 255, 0.98);\r\n  --theme-surface-bg-solid: #ffffff;\r\n  --theme-surface-border: rgba(99, 24, 50, 0.14);\r\n  --theme-surface-border-strong: rgba(99, 24, 50, 0.20);\r\n\r\n  /* Accent tints used by dashboards/cards (avoid hard-coded purple RGBA) */\r\n  --theme-accent-border-subtle: rgba(64, 26, 89, 0.20);\r\n  --theme-accent-border: rgba(64, 26, 89, 0.30);\r\n  --theme-accent-bg-soft: rgba(64, 26, 89, 0.18);\r\n  --theme-accent-shadow-soft: rgba(64, 26, 89, 0.10);\r\n  --theme-accent-shadow-strong: rgba(64, 26, 89, 0.30);\r\n  --theme-accent-color-hover: #4d216a;\r\n\r\n  /* RGBA helpers for acrylic gradients (avoid hard-coded brand rgba() in components) */\r\n  --theme-primary-rgba-70: rgba(99, 24, 50, 0.70);\r\n  --theme-secondary-rgba-70: rgba(91, 36, 43, 0.70);\r\n  --theme-primary-rgba-95: rgba(99, 24, 50, 0.95);\r\n  --theme-secondary-rgba-95: rgba(91, 36, 43, 0.95);\r\n\r\n  /* Navbar link pill background (active/hover) */\r\n  --theme-navlink-active-bg: rgba(99, 24, 50, 0.08);\r\n  /* Theme accent colors for components */\r\n  --theme-primary-color: var(--brand-maroon);\r\n  --theme-secondary-color: var(--wine);\r\n  --theme-accent-color: var(--brand-purple);\r\n  --theme-text-primary: var(--text-dark);\r\n  --theme-text-secondary: #4a4f5c;\r\n  --theme-shadow: var(--shadow);\r\n\r\n  /* Dashboard layout tokens (shared) */\r\n  --dashboard-padding-top-offset: 12px;\r\n  --dashboard-padding-x: 8px;\r\n  --dashboard-padding-bottom: 16px;\r\n  --dashboard-grid-gap: 12px;\r\n  --dashboard-main-gap: 12px;\r\n  --dashboard-main-col-min: 240px;\r\n  --dashboard-stat-min: 240px;\r\n  --dashboard-stat-gap: 12px;\r\n  --dashboard-stat-padding: 12px 16px;\r\n  --dashboard-stat-radius: 16px;\r\n  --dashboard-card-padding: 16px 18px;\r\n  --dashboard-card-header-margin: 12px;\r\n  --dashboard-card-header-padding: 10px;\r\n  --dashboard-quick-actions-gap: 8px;\r\n  --dashboard-container-max: 1880px;\r\n  --dashboard-container-max-client: 1800px;\r\n  --dashboard-container-max-agent: 1880px;\r\n  --dashboard-container-max-admin: 1920px;\r\n\r\n  /* Highlights / glows (avoid hard-coded pink/gold values in components) */\r\n  --theme-highlight-soft: #ffc4d6;\r\n  --theme-highlight-light: var(--baby-pink);\r\n  --theme-highlight-strong: #ffd700;\r\n  --theme-highlight-strong-border: rgba(255, 215, 0, 0.10);\r\n  --theme-highlight-glow: rgba(254, 245, 249, 0.6);\r\n  --theme-highlight-glow-subtle: rgba(254, 245, 249, 0.18);\r\n  --theme-highlight-border: rgba(254, 245, 249, 0.36);\r\n  --theme-primary-shadow-color: rgba(99, 24, 50, 0.35);\r\n\r\n  /* High-contrast ink (default/light uses brand ink; dark themes override) */\r\n  --theme-contrast-strong: var(--brand-maroon);\r\n\r\n  /* Text-on-color tokens (avoid hard-coded #fff / black in components) */\r\n  --theme-on-success: var(--white);\r\n  --theme-on-danger: var(--white);\r\n  /* Warning background is bright (yellow), so text should stay dark even on dark themes */\r\n  --theme-on-warning: rgba(0, 0, 0, 0.85);\r\n\r\n  /* Progress bar tokens */\r\n  --theme-progress-edge: rgba(254, 245, 249, 0.8);\r\n  --theme-progress-center: rgba(255, 255, 255, 0.95);\r\n  --theme-progress-glow-strong: rgba(254, 245, 249, 0.7);\r\n  --theme-progress-glow-soft: rgba(254, 245, 249, 0.4);\r\n\r\n  /* Particles overlay tuning */\r\n  --particles-opacity: 0.55;\r\n  --particles-opacity-home: 0.70;\r\n  /* `screen` can become effectively invisible over light UI surfaces.\r\n     Default theme uses `normal` for consistent visibility; dark-forest overrides this. */\r\n  --particles-blend-mode: normal;\r\n}\r\n\r\n/* Dark-forest accessibility overrides */\r\nhtml[data-theme=\"dark-forest\"] {\r\n  --text-dark: #f8fafc;\r\n  --text-light: rgba(255, 255, 255, 0.82);\r\n  --theme-text-primary: #f8fafc;\r\n  --theme-text-secondary: rgba(255, 255, 255, 0.82);\r\n  --theme-text-tertiary: rgba(255, 255, 255, 0.68);\r\n  --theme-contrast-strong: #ffffff;\r\n}\r\n\r\n/* Hidden class for page sections (show/hide navigation) */\r\n.page-section.hidden {\r\n  display: none !important;\r\n}\r\n\r\n/* Loading modal overlay - Complete version with progress and animations */\r\nbody.loading-locked {\r\n  overflow: hidden;\r\n}\r\n\r\nhtml[data-theme=\"dark-forest\"] {\r\n  --theme-text-secondary: rgba(255, 255, 255, 0.82);\r\n  --theme-text-tertiary: rgba(255, 255, 255, 0.68);\r\n}\r\n\r\n.loading-modal {\r\n  position: fixed;\r\n  inset: 0;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  z-index: 9999;\r\n  background: rgba(6, 4, 6, 0.55);\r\n  backdrop-filter: blur(10px);\r\n  -webkit-backdrop-filter: blur(10px);\r\n  opacity: 0;\r\n  pointer-events: none;\r\n  transition: opacity 0.6s ease;\r\n}\r\n\r\n.loading-modal.visible {\r\n  opacity: 1;\r\n  pointer-events: auto;\r\n}\r\n\r\n.modal-card {\r\n  width: min(820px, calc(100% - 48px));\r\n  background: rgba(255, 255, 255, 0.94);\r\n  border-radius: 14px;\r\n  padding: 20px;\r\n  box-shadow: 0 30px 80px rgba(10, 10, 10, 0.16), 0 10px 30px rgba(10, 10, 10, 0.1), inset 0 1px 0 rgba(255, 255, 255, 0.72);\r\n  color: #0f172a;\r\n  border: 1px solid rgba(0, 0, 0, 0.06);\r\n  backdrop-filter: blur(12px) saturate(1.02);\r\n  -webkit-backdrop-filter: blur(12px) saturate(1.02);\r\n  position: relative;\r\n  transform: scale(0.985);\r\n  opacity: 0.96;\r\n  transition: transform 0.55s cubic-bezier(0.33, 1, 0.68, 1), opacity 0.55s ease;\r\n}\r\n\r\n.loading-modal.visible .modal-card {\r\n  transform: scale(1);\r\n  opacity: 1;\r\n}\r\n\r\n.logo-and-info {\r\n  display: flex;\r\n  gap: 20px;\r\n  align-items: center;\r\n  width: 100%;\r\n}\r\n\r\n.modal-info {\r\n  flex: 1;\r\n  min-width: 0;\r\n}\r\n\r\n/* Shared logo renderer slots (keeps size + contrast consistent across pages) */\r\n.logo-slot {\r\n  display: inline-flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  color: var(--logo-contrast-primary, var(--brand-maroon));\r\n}\r\n\r\n.logo-slot[data-logo-contrast=\"ink\"] {\r\n  color: var(--logo-contrast-ink, #0f172a);\r\n}\r\n\r\n.logo-slot[data-logo-contrast=\"on-dark\"] {\r\n  color: var(--logo-contrast-on-dark, #ffffff);\r\n  text-shadow: 0 6px 18px rgba(0, 0, 0, 0.35);\r\n}\r\n\r\n.logo-slot[data-logo-contrast=\"muted\"] {\r\n  color: var(--logo-contrast-muted, rgba(15, 23, 42, 0.72));\r\n}\r\n\r\n.krause-shield {\r\n  display: block;\r\n  width: var(--logo-size-md);\r\n  height: var(--logo-size-md);\r\n  color: inherit;\r\n  filter: drop-shadow(0 6px 14px rgba(0, 0, 0, 0.08));\r\n}\r\n\r\n.logo-slot[data-logo-size=\"sm\"] .krause-shield,\r\n.krause-shield.logo-size-sm {\r\n  width: var(--logo-size-sm);\r\n  height: var(--logo-size-sm);\r\n}\r\n\r\n.logo-slot[data-logo-size=\"md\"] .krause-shield,\r\n.krause-shield.logo-size-md {\r\n  width: var(--logo-size-md);\r\n  height: var(--logo-size-md);\r\n}\r\n\r\n.logo-slot[data-logo-size=\"lg\"] .krause-shield,\r\n.krause-shield.logo-size-lg {\r\n  width: var(--logo-size-lg);\r\n  height: var(--logo-size-lg);\r\n}\r\n\r\n.logo-shield,\r\n.shield-svg {\r\n  color: inherit;\r\n  width: var(--logo-size-md);\r\n  height: var(--logo-size-md);\r\n}\r\n\r\n.logo-shield-footer {\r\n  width: var(--logo-size-md);\r\n  height: var(--logo-size-md);\r\n  color: inherit;\r\n}\r\n\r\n/* Particles floating in background */\r\n.bg-particles {\r\n  position: absolute;\r\n  inset: 0;\r\n  pointer-events: none;\r\n  z-index: 1100;\r\n  overflow: hidden;\r\n}\r\n\r\n.particle {\r\n  position: absolute;\r\n  width: 8px;\r\n  height: 8px;\r\n  border-radius: 50%;\r\n  background: rgba(139, 35, 72, 0.12);\r\n  box-shadow: 0 6px 18px rgba(139, 35, 72, 0.14), 0 2px 6px rgba(0, 0, 0, 0.06);\r\n  transform: translateY(0);\r\n  will-change: transform, opacity;\r\n  opacity: 0.9;\r\n}\r\n\r\n@keyframes floatUp {\r\n  0% {\r\n    transform: translateY(20px) scale(0.9);\r\n    opacity: 0.7;\r\n  }\r\n\r\n  50% {\r\n    opacity: 0.95;\r\n  }\r\n\r\n  100% {\r\n    transform: translateY(-24px) scale(1.05);\r\n    opacity: 0.45;\r\n  }\r\n}\r\n\r\n/* Official logo parts stroke draw for LOADING MODAL ONLY */\r\n/* Shield primitives pick up currentColor for consistent contrast */\r\n.loading-modal .shield-circle,\r\n.loading-modal .shield-diamond,\r\n.loading-modal .shield-arc,\r\n.loading-modal .shield-line {\r\n  fill: none;\r\n  stroke: currentColor;\r\n  stroke-width: 3;\r\n  stroke-dasharray: 220;\r\n  stroke-dashoffset: 220;\r\n  opacity: 0;\r\n  transition: opacity 0.4s ease;\r\n  filter: drop-shadow(0 6px 18px rgba(0, 0, 0, 0.12));\r\n}\r\n\r\n.loading-modal .shield-circle.active,\r\n.loading-modal .shield-diamond.active,\r\n.loading-modal .shield-arc.active,\r\n.loading-modal .shield-line.active {\r\n  animation: loadingDrawStroke 0.9s ease-out forwards;\r\n  opacity: 1;\r\n}\r\n\r\n.loading-modal .shield-center {\r\n  fill: currentColor;\r\n  opacity: 0;\r\n  transform-origin: 50% 50%;\r\n}\r\n\r\n.loading-modal .shield-center.active {\r\n  animation: centerPop 0.6s ease-out forwards;\r\n}\r\n\r\n@keyframes loadingDrawStroke {\r\n  from {\r\n    stroke-dashoffset: 220;\r\n    opacity: 0.2;\r\n  }\r\n\r\n  to {\r\n    stroke-dashoffset: 0;\r\n    opacity: 1;\r\n  }\r\n}\r\n\r\n@keyframes centerPop {\r\n  0% {\r\n    transform: scale(0.4);\r\n    opacity: 0;\r\n  }\r\n\r\n  60% {\r\n    transform: scale(1.12);\r\n    opacity: 1;\r\n  }\r\n\r\n  100% {\r\n    transform: scale(1);\r\n    opacity: 1;\r\n  }\r\n}\r\n\r\n.modal-info h2 {\r\n  font-family: 'Cinzel', serif;\r\n  margin-bottom: 8px;\r\n  font-size: 1.4rem;\r\n  color: #111;\r\n  font-weight: 700;\r\n}\r\n\r\n.progress-percentage {\r\n  font-size: 2.2rem;\r\n  font-weight: 700;\r\n  font-family: 'Cinzel', serif;\r\n  margin-top: 8px;\r\n  color: #0f172a;\r\n  line-height: 1;\r\n}\r\n\r\n.status-text {\r\n  margin-top: 8px;\r\n  color: rgba(15, 23, 42, 0.82);\r\n  font-size: 0.95rem;\r\n  font-weight: 500;\r\n}\r\n\r\n.progress-bar-container {\r\n  width: 100%;\r\n  height: 8px;\r\n  background: rgba(0, 0, 0, 0.06);\r\n  border-radius: 999px;\r\n  overflow: hidden;\r\n  margin-top: 12px;\r\n  border: 1px solid rgba(0, 0, 0, 0.02);\r\n  box-shadow: inset 0 -2px 8px rgba(0, 0, 0, 0.06);\r\n}\r\n\r\n.progress-bar-fill {\r\n  height: 100%;\r\n  width: 0%;\r\n  background: linear-gradient(90deg, #8b2348 0%, #9b59b6 50%, #8b2348 100%);\r\n  transition: width 260ms linear;\r\n  box-shadow: 0 6px 18px rgba(139, 35, 72, 0.12);\r\n}\r\n\r\n.did-you-know {\r\n  margin-top: 14px;\r\n  background: #ffffff;\r\n  padding: 12px;\r\n  border-radius: 10px;\r\n  color: #111;\r\n  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.06);\r\n}\r\n\r\n.did-you-know-title {\r\n  font-weight: 700;\r\n  margin-bottom: 6px;\r\n  color: #0f172a;\r\n}\r\n\r\n#funFact {\r\n  color: rgba(15, 23, 42, 0.85);\r\n  font-size: 0.9rem;\r\n  line-height: 1.5;\r\n}\r\n\r\n@media (max-width: 700px) {\r\n  .logo-and-info {\r\n    flex-direction: row;\r\n  }\r\n\r\n  .logo-slot[data-logo-size=\"lg\"] .krause-shield,\r\n  .shield-svg.logo-size-lg,\r\n  .shield-svg {\r\n    width: var(--logo-size-md);\r\n    height: var(--logo-size-md);\r\n  }\r\n}\r\n\r\n/* Footer (restored) */\r\n.footer {\r\n  background: var(--footer-acrylic-bg, var(--acrylic-bg-dark));\r\n  backdrop-filter: blur(var(--footer-acrylic-blur, 25px)) saturate(var(--footer-acrylic-saturate, 180%)) !important;\r\n  -webkit-backdrop-filter: blur(var(--footer-acrylic-blur, 25px)) saturate(var(--footer-acrylic-saturate, 180%)) !important;\r\n  color: white;\r\n  position: fixed;\r\n  bottom: 0;\r\n  left: 0;\r\n  right: 0;\r\n  z-index: 9996;\r\n  transition: transform 0.3s ease;\r\n  border-top: 1px solid rgba(255, 255, 255, 0.15);\r\n  box-shadow: 0 -4px 20px rgba(0, 0, 0, 0.2);\r\n  pointer-events: auto;\r\n}\r\n\r\n/* Ensure home page also renders the acrylic footer (some browsers drop backdrop-filter without a scoped reassertion) */\r\nbody[data-page=\"home\"] .footer {\r\n  background: var(--footer-acrylic-bg);\r\n  backdrop-filter: blur(var(--footer-acrylic-blur)) saturate(var(--footer-acrylic-saturate)) !important;\r\n  -webkit-backdrop-filter: blur(var(--footer-acrylic-blur)) saturate(var(--footer-acrylic-saturate)) !important;\r\n}\r\n\r\n.footer.collapsed .footer-container {\r\n  display: none;\r\n}\r\n\r\n.footer-toggle {\r\n  background: transparent;\r\n  border: none;\r\n  border-bottom: 1px solid rgba(255, 255, 255, 0.1);\r\n  color: white;\r\n  padding: 12px 24px;\r\n  width: 100%;\r\n  text-align: center;\r\n  cursor: pointer;\r\n  font-weight: 600;\r\n  font-size: clamp(13px, 2vw, 15px);\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  gap: 8px;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.footer-toggle:hover {\r\n  background: rgba(255, 255, 255, 0.15);\r\n  border-bottom-color: rgba(255, 255, 255, 0.25);\r\n  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.12),\r\n    0 2px 8px rgba(255, 255, 255, 0.1);\r\n}\r\n\r\n.footer-toggle svg {\r\n  transition: transform 0.3s ease;\r\n}\r\n\r\n.footer.collapsed .footer-toggle svg {\r\n  transform: rotate(180deg);\r\n}\r\n\r\n.footer-container {\r\n  max-width: 1200px;\r\n  margin: 0 auto;\r\n  padding: clamp(12px, 2vh, 18px) clamp(16px, 3vw, 24px);\r\n}\r\n\r\n.footer-grid {\r\n  display: grid;\r\n  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));\r\n  gap: clamp(20px, 3vw, 32px);\r\n  margin-bottom: clamp(12px, 2vh, 16px);\r\n  padding-bottom: clamp(12px, 2vh, 16px);\r\n  border-bottom: 1px solid rgba(255, 255, 255, 0.15);\r\n}\r\n\r\n.footer-section h4 {\r\n  font-family: 'Cinzel', serif;\r\n  font-size: clamp(0.95rem, 1.8vw, 1.1rem);\r\n  margin-bottom: clamp(10px, 1.5vh, 14px);\r\n  color: rgba(255, 255, 255, 0.95);\r\n}\r\n\r\n.footer-section ul {\r\n  list-style: none;\r\n  padding: 0;\r\n  margin: 0;\r\n}\r\n\r\n.footer-section li {\r\n  margin-bottom: clamp(6px, 1vh, 9px);\r\n}\r\n\r\n.footer-section a {\r\n  color: rgba(255, 255, 255, 0.85);\r\n  text-decoration: none;\r\n  transition: color 0.3s ease;\r\n  font-size: clamp(0.8rem, 1.4vw, 0.9rem);\r\n}\r\n\r\n.footer-section a:hover {\r\n  color: white;\r\n}\r\n\r\n.footer-brand {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: clamp(8px, 1.2vh, 12px);\r\n  align-items: center;\r\n  /* center logo and text horizontally */\r\n  text-align: center;\r\n}\r\n\r\n.logo-shield-footer {\r\n  color: white;\r\n  margin-bottom: clamp(6px, 1vh, 10px);\r\n  width: clamp(52px, 7vw, 64px);\r\n  height: clamp(52px, 7vw, 64px);\r\n  display: block;\r\n  flex-shrink: 0;\r\n}\r\n\r\n/* Page visibility control */\r\n/* Page visibility control */\r\n#mainContent>[data-page] {\r\n  width: 100%;\r\n}\r\n\r\n/* Hide by default using utility class */\r\n.d-none {\r\n  display: none !important;\r\n}\r\n\r\n* {\r\n  margin: 0;\r\n  padding: 0;\r\n  box-sizing: border-box;\r\n}\r\n\r\nhtml {\r\n  margin: 0 !important;\r\n  padding: 0 !important;\r\n  height: 100% !important;\r\n  overflow: hidden !important;\r\n  width: 100% !important;\r\n}\r\n\r\nbody {\r\n  margin: 0 !important;\r\n  padding: 0 !important;\r\n  font-family: 'Aptos Display', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;\r\n  line-height: 1.65;\r\n  color: var(--text-dark);\r\n  background: linear-gradient(135deg,\r\n      var(--gradient-start) 0%,\r\n      var(--gradient-mid) 40%,\r\n      var(--gradient-end) 100%);\r\n  background-size: 300% 300%;\r\n  background-attachment: fixed !important;\r\n  height: 100vh !important;\r\n  overflow: hidden !important;\r\n  width: 100vw !important;\r\n  position: fixed !important;\r\n  top: 0 !important;\r\n  left: 0 !important;\r\n  animation: gradientShift 20s ease infinite;\r\n  transition: background-position 1.5s ease-in-out;\r\n}\r\n\r\n/* Slightly larger body copy for descriptive sections */\r\np,\r\n.section-description,\r\n.hero-description,\r\n.feature-box p,\r\n.service-card p,\r\n.about-section p,\r\n.contact-info p,\r\n.quote-lead,\r\n.quote-footnote,\r\n.quote-benefits,\r\n.stat-box p,\r\n.agent-card p {\r\n  font-size: 1.04rem;\r\n  line-height: 1.65;\r\n}\r\n\r\n@keyframes gradientShift {\r\n  0% {\r\n    background-position: 0% 50%;\r\n  }\r\n\r\n  25% {\r\n    background-position: 50% 25%;\r\n  }\r\n\r\n  50% {\r\n    background-position: 100% 50%;\r\n  }\r\n\r\n  75% {\r\n    background-position: 50% 75%;\r\n  }\r\n\r\n  100% {\r\n    background-position: 0% 50%;\r\n  }\r\n}\r\n\r\nbody.section-hero {\r\n  background-position: 0% 30%;\r\n}\r\n\r\nbody.section-parade {\r\n  background-position: 50% 50%;\r\n}\r\n\r\nbody.section-cta {\r\n  background-position: 100% 70%;\r\n}\r\n\r\n/* Particles */\r\n#particles-canvas {\r\n  position: fixed;\r\n  top: 0;\r\n  left: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n  pointer-events: none;\r\n  /* Above page backgrounds (dashboards use solid surfaces) but below navbar/subnav */\r\n  z-index: 50;\r\n  opacity: var(--particles-opacity);\r\n  mix-blend-mode: var(--particles-blend-mode);\r\n  transition: opacity 0.3s ease;\r\n}\r\n\r\nbody[data-page=\"home\"] #particles-canvas {\r\n  opacity: var(--particles-opacity-home);\r\n}\r\n\r\n/* Hide particles on fullpage sections (route-aware).\r\n   NOTE: We can't use :has(.services-fullpage) here because templates are injected\r\n   into the DOM (hidden) and would permanently match, hiding particles everywhere. */\r\nbody[data-page=\"services\"] #particles-canvas,\r\nbody[data-page=\"about\"] #particles-canvas,\r\nbody[data-page=\"contact\"] #particles-canvas,\r\n/* Auth screens read cleaner without animated canvas behind */\r\nbody[data-page=\"login\"] #particles-canvas,\r\nbody[data-page=\"agent-login\"] #particles-canvas {\r\n  display: none;\r\n  pointer-events: none;\r\n}\r\n\r\n/* Dashboards: reduce opacity for subtle ambient effect */\r\nbody[data-page=\"client-dashboard\"] #particles-canvas,\r\nbody[data-page=\"agent-dashboard\"] #particles-canvas,\r\nbody[data-page=\"admin-dashboard\"] #particles-canvas {\r\n  opacity: 0.35;\r\n}\r\n\r\n/* Sub Navigation */\r\n.sub-navigation {\r\n  position: fixed;\r\n  top: var(--nav-height);\r\n  left: 0;\r\n  right: 0;\r\n  width: 100%;\r\n  z-index: 9998;\r\n  background: var(--acrylic-bg-dark);\r\n  backdrop-filter: blur(25px) saturate(180%);\r\n  -webkit-backdrop-filter: blur(25px) saturate(180%);\r\n  border-bottom: 1px solid rgba(255, 255, 255, 0.15);\r\n  padding: 0;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  gap: 0;\r\n  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);\r\n  height: var(--subnav-height);\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.sub-nav-btn {\r\n  position: relative;\r\n  padding: 18px 32px;\r\n  background: transparent;\r\n  border: none;\r\n  border-radius: 0 !important;\r\n  font-size: 13px;\r\n  font-weight: 600;\r\n  color: rgba(255, 255, 255, 0.85);\r\n  cursor: pointer;\r\n  will-change: transform, opacity;\r\n  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);\r\n  font-family: inherit;\r\n  text-transform: uppercase;\r\n  letter-spacing: 1.5px;\r\n  overflow: hidden;\r\n  height: 56px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n}\r\n\r\n.sub-nav-btn::before {\r\n  content: '';\r\n  position: absolute;\r\n  bottom: 0;\r\n  left: 0;\r\n  right: 0;\r\n  width: 100%;\r\n  height: 3px;\r\n  background: linear-gradient(90deg, transparent, var(--baby-pink), transparent);\r\n  opacity: 0;\r\n  transform: scaleX(0);\r\n  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);\r\n  pointer-events: none;\r\n}\r\n\r\n.sub-nav-btn:hover {\r\n  background: rgba(255, 255, 255, 0.1);\r\n  backdrop-filter: blur(20px) saturate(160%);\r\n  -webkit-backdrop-filter: blur(20px) saturate(160%);\r\n  color: white;\r\n}\r\n\r\n.sub-nav-btn:hover::before {\r\n  opacity: 0.5;\r\n  transform: scaleX(1);\r\n}\r\n\r\n.sub-nav-btn.active {\r\n  background: rgba(255, 255, 255, 0.15);\r\n  backdrop-filter: blur(25px) saturate(170%);\r\n  -webkit-backdrop-filter: blur(25px) saturate(170%);\r\n  color: white;\r\n  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);\r\n  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.1);\r\n}\r\n\r\n.sub-nav-btn.active::before {\r\n  opacity: 1;\r\n  transform: scaleX(1);\r\n  box-shadow: 0 0 15px rgba(254, 245, 249, 0.6);\r\n}\r\n\r\n.sub-nav-btn:not(:last-child)::after {\r\n  content: '|';\r\n  position: absolute;\r\n  right: 0;\r\n  top: 50%;\r\n  transform: translateY(-50%);\r\n  color: rgba(255, 255, 255, 0.25);\r\n  font-weight: 300;\r\n  font-size: 18px;\r\n  pointer-events: none;\r\n  width: 1px;\r\n  height: 100%;\r\n}\r\n\r\n.sub-navigation.show {\r\n  animation: subNavSlideDown 0.5s cubic-bezier(0.4, 0, 0.2, 1) forwards;\r\n}\r\n\r\n@keyframes subNavSlideDown {\r\n  from {\r\n    opacity: 0;\r\n    transform: translateY(-10px);\r\n  }\r\n\r\n  to {\r\n    opacity: 1;\r\n    transform: translateY(0);\r\n  }\r\n}\r\n\r\n/* Navbar */\r\n.navbar {\r\n  position: fixed;\r\n  top: 0;\r\n  left: 0;\r\n  right: 0;\r\n  width: 100%;\r\n  height: var(--nav-height);\r\n  z-index: 9999;\r\n  background: rgba(255, 255, 255, 0.95);\r\n  backdrop-filter: blur(20px);\r\n  border-bottom: 1px solid rgba(0, 0, 0, 0.06);\r\n  padding: 0;\r\n  margin: 0;\r\n  transition: all 0.3s ease;\r\n  box-shadow: var(--shadow-sm);\r\n  display: flex;\r\n  align-items: center;\r\n}\r\n\r\n.navbar.scrolled {\r\n  box-shadow: var(--shadow);\r\n}\r\n\r\n.nav-container {\r\n  max-width: 100%;\r\n  width: 100%;\r\n  height: 100%;\r\n  margin: 0 auto;\r\n  padding: 0 48px;\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  box-sizing: border-box;\r\n}\r\n\r\n.nav-brand {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 12px;\r\n  cursor: pointer;\r\n  transition: transform 0.3s ease;\r\n}\r\n\r\n.nav-brand .krause-shield {\r\n  width: var(--logo-size-sm);\r\n  height: var(--logo-size-sm);\r\n}\r\n\r\n.nav-brand:hover {\r\n  transform: translateY(-2px);\r\n}\r\n\r\n\r\n.logo-shield {\r\n  width: var(--logo-size-md);\r\n  height: var(--logo-size-md);\r\n  max-width: calc(var(--logo-size-lg) - 16px);\r\n  max-height: calc(var(--logo-size-lg) - 16px);\r\n  color: inherit;\r\n  filter: drop-shadow(0 2px 4px var(--theme-primary-shadow-color));\r\n}\r\n\r\n/* Logo general - SIN auto-animacin, solo estilos base */\r\n.krause-shield .shield-circle,\r\n.krause-shield .shield-diamond,\r\n.krause-shield .shield-arc,\r\n.krause-shield .shield-line {\r\n  fill: none;\r\n  stroke: currentColor;\r\n  stroke-width: 3;\r\n  /* NO auto-animar - dejar que sean estticos o controlados por JS */\r\n}\r\n\r\n/* Hover solo cuando NO est en loading */\r\nbody:not(.loading-locked) .krause-shield:hover .shield-circle,\r\nbody:not(.loading-locked) .krause-shield:hover .shield-diamond,\r\nbody:not(.loading-locked) .krause-shield:hover .shield-arc,\r\nbody:not(.loading-locked) .krause-shield:hover .shield-line {\r\n  animation: logoPulse 0.6s ease-in-out;\r\n}\r\n\r\n@keyframes logoPulse {\r\n\r\n  0%,\r\n  100% {\r\n    transform: scale(1);\r\n    opacity: 1;\r\n  }\r\n\r\n  50% {\r\n    transform: scale(1.05);\r\n    opacity: 0.8;\r\n  }\r\n}\r\n\r\n.brand-text {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 2px;\r\n}\r\n\r\n.brand-name {\r\n  font-family: 'Cinzel', serif;\r\n  font-size: 22px;\r\n  font-weight: 600;\r\n  color: var(--brand-maroon);\r\n  line-height: 1;\r\n}\r\n\r\n.brand-tagline {\r\n  font-size: 11px;\r\n  color: var(--text-light);\r\n  font-weight: 500;\r\n  letter-spacing: 0.5px;\r\n  text-transform: uppercase;\r\n}\r\n\r\n.mobile-menu-toggle {\r\n  display: none;\r\n  flex-direction: column;\r\n  gap: 5px;\r\n  background: none;\r\n  border: none;\r\n  cursor: pointer;\r\n  padding: 8px;\r\n}\r\n\r\n.mobile-menu-toggle span {\r\n  width: 25px;\r\n  height: 3px;\r\n  background: var(--brand-maroon);\r\n  border-radius: 2px;\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.nav-menu {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 12px;\r\n}\r\n\r\n.nav-link {\r\n  padding: 10px 20px;\r\n  color: var(--text-dark);\r\n  text-decoration: none;\r\n  font-weight: 500;\r\n  transition: all 0.3s ease;\r\n  border-radius: 8px;\r\n  font-size: 16px;\r\n}\r\n\r\n.nav-link:hover,\r\n.nav-link.active {\r\n  color: var(--brand-maroon);\r\n  background: var(--theme-navlink-active-bg);\r\n  backdrop-filter: blur(15px) saturate(150%);\r\n  -webkit-backdrop-filter: blur(15px) saturate(150%);\r\n}\r\n\r\n.nav-buttons {\r\n  display: flex;\r\n  gap: 12px;\r\n  margin-left: 20px;\r\n}\r\n\r\n/* Generic buttons (used in Home CTA + Auth forms) */\r\n.btn {\r\n  display: inline-flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  gap: 10px;\r\n  padding: 12px 18px;\r\n  border-radius: 12px;\r\n  border: 1px solid transparent;\r\n  font-family: inherit;\r\n  font-weight: 700;\r\n  font-size: 0.95rem;\r\n  line-height: 1;\r\n  cursor: pointer;\r\n  user-select: none;\r\n  text-decoration: none;\r\n  background: transparent;\r\n  color: inherit;\r\n  transition: transform 0.15s ease, box-shadow 0.25s ease, background 0.25s ease,\r\n    border-color 0.25s ease, color 0.25s ease, filter 0.25s ease;\r\n}\r\n\r\n.btn:hover {\r\n  transform: translateY(-1px);\r\n}\r\n\r\n.btn:active {\r\n  transform: translateY(0);\r\n}\r\n\r\n.btn:disabled,\r\n.btn[disabled] {\r\n  opacity: 0.65;\r\n  cursor: not-allowed;\r\n  transform: none;\r\n}\r\n\r\n.btn:focus-visible {\r\n  outline: 2px solid var(--theme-highlight-light);\r\n  outline-offset: 2px;\r\n}\r\n\r\n.btn-lg {\r\n  padding: 14px 22px;\r\n  border-radius: 14px;\r\n  font-size: 1rem;\r\n}\r\n\r\n.btn-block {\r\n  width: 100%;\r\n}\r\n\r\n.btn-primary {\r\n  background: radial-gradient(circle at 35% 35%, var(--theme-primary-color) 0%, var(--theme-secondary-color) 70%);\r\n  color: var(--white);\r\n  border-color: rgba(255, 255, 255, 0.16);\r\n  box-shadow: 0 10px 24px rgba(0, 0, 0, 0.22), 0 10px 24px var(--theme-primary-shadow-color);\r\n}\r\n\r\n.btn.is-loading,\r\n.btn[aria-busy=\"true\"] {\r\n  position: relative;\r\n  pointer-events: none;\r\n  color: var(--theme-contrast-strong, var(--brand-maroon));\r\n  border-color: var(--theme-contrast-glow, rgba(255, 255, 255, 0.25));\r\n  filter: brightness(1.02);\r\n}\r\n\r\n.btn.is-loading::after,\r\n.btn[aria-busy=\"true\"]::after {\r\n  content: '';\r\n  position: absolute;\r\n  inset: 6px;\r\n  border-radius: inherit;\r\n  background: radial-gradient(circle at 40% 40%, rgba(255, 255, 255, 0.35), rgba(255, 255, 255, 0));\r\n  animation: loading-pulse 0.9s ease-in-out infinite;\r\n  mix-blend-mode: screen;\r\n}\r\n\r\n@keyframes loading-pulse {\r\n\r\n  0%,\r\n  100% {\r\n    opacity: 0.35;\r\n    transform: scale(0.98);\r\n  }\r\n\r\n  50% {\r\n    opacity: 0.75;\r\n    transform: scale(1.02);\r\n  }\r\n}\r\n\r\n.btn-primary:hover {\r\n  filter: brightness(1.05);\r\n  box-shadow: 0 14px 34px rgba(0, 0, 0, 0.26), 0 14px 34px var(--theme-primary-shadow-color);\r\n}\r\n\r\n.btn-secondary {\r\n  background: rgba(255, 255, 255, 0.14);\r\n  color: white;\r\n  border-color: rgba(255, 255, 255, 0.20);\r\n  box-shadow: 0 10px 24px rgba(0, 0, 0, 0.18);\r\n}\r\n\r\n.btn-secondary:hover {\r\n  background: rgba(255, 255, 255, 0.18);\r\n  border-color: rgba(255, 255, 255, 0.26);\r\n  box-shadow: 0 14px 34px rgba(0, 0, 0, 0.22);\r\n}\r\n\r\n.btn-outline {\r\n  background: transparent;\r\n  color: var(--theme-primary-color);\r\n  border-color: rgba(255, 255, 255, 0.35);\r\n}\r\n\r\n.btn-outline:hover {\r\n  background: rgba(255, 255, 255, 0.12);\r\n  color: var(--theme-secondary-color);\r\n}\r\n\r\n.btn-nav {\r\n  padding: 12px 24px;\r\n  border-radius: 8px;\r\n  font-weight: 600;\r\n  font-size: 15px;\r\n  cursor: pointer;\r\n  transition: all 0.3s ease;\r\n  border: none;\r\n  font-family: inherit;\r\n}\r\n\r\n.btn-login {\r\n  background: transparent;\r\n  color: var(--brand-maroon);\r\n  border: 2px solid var(--brand-maroon);\r\n}\r\n\r\n.btn-login:hover {\r\n  background: var(--brand-maroon);\r\n  color: white;\r\n}\r\n\r\n.btn-agent {\r\n  background: var(--brand-maroon);\r\n  color: white;\r\n  border: 2px solid var(--brand-maroon);\r\n}\r\n\r\n.btn-agent:hover {\r\n  background: var(--wine);\r\n  border-color: var(--wine);\r\n  transform: translateY(-2px);\r\n  box-shadow: 0 4px 12px var(--theme-primary-shadow-color);\r\n}\r\n\r\n/* Main Content */\r\n.main-content {\r\n  position: fixed;\r\n  top: var(--nav-height);\r\n  left: 0;\r\n  right: 0;\r\n  bottom: 0;\r\n  z-index: 10;\r\n  height: auto;\r\n  width: 100%;\r\n  box-sizing: border-box;\r\n  overflow-y: auto;\r\n  overflow-x: hidden;\r\n}\r\n\r\nbody:not(.no-footer) .main-content {\r\n  padding-bottom: var(--footer-height);\r\n}\r\n\r\nbody.no-footer .main-content {\r\n  padding-bottom: 0;\r\n}\r\n\r\nbody.no-footer .footer {\r\n  display: none !important;\r\n}\r\n\r\n/* Home has a fixed sub-navigation that overlays the scroll container.\r\n   Add top padding only on home so sections (esp. Cotiza logo/title) are not cut. */\r\nbody[data-page=\"home\"] .main-content {\r\n  padding-top: var(--subnav-height);\r\n}\r\n\r\n/* Dashboards sin navbar: usar todo el alto disponible sin offset del nav */\r\nbody[data-page=\"client-dashboard\"] .main-content,\r\nbody[data-page=\"agent-dashboard\"] .main-content,\r\nbody[data-page=\"admin-dashboard\"] .main-content {\r\n  top: 0;\r\n  padding-top: 0;\r\n}\r\n\r\n\r\n\r\n/* Sub Navigation */\r\n.sub-navigation {\r\n  position: fixed;\r\n  top: var(--nav-height);\r\n  left: 0;\r\n  right: 0;\r\n  width: 100%;\r\n  z-index: 9998;\r\n  background: var(--acrylic-bg-dark);\r\n  backdrop-filter: blur(25px) saturate(180%);\r\n  -webkit-backdrop-filter: blur(25px) saturate(180%);\r\n  border-bottom: 1px solid rgba(255, 255, 255, 0.15);\r\n  padding: 0;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  gap: 0;\r\n  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);\r\n  height: var(--subnav-height);\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.sub-nav-btn {\r\n  position: relative;\r\n  padding: 18px 32px;\r\n  background: transparent;\r\n  border: none;\r\n  border-radius: 0 !important;\r\n  font-size: 13px;\r\n  font-weight: 600;\r\n  color: rgba(255, 255, 255, 0.85);\r\n  cursor: pointer;\r\n  will-change: transform, opacity;\r\n  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);\r\n  font-family: inherit;\r\n  text-transform: uppercase;\r\n  letter-spacing: 1.5px;\r\n  overflow: hidden;\r\n  height: 56px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n}\r\n\r\n.sub-nav-btn::before {\r\n  content: '';\r\n  position: absolute;\r\n  bottom: 0;\r\n  left: 0;\r\n  right: 0;\r\n  width: 100%;\r\n  height: 3px;\r\n  background: linear-gradient(90deg, transparent, var(--theme-highlight-light), transparent);\r\n  opacity: 0;\r\n  transform: scaleX(0);\r\n  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);\r\n  pointer-events: none;\r\n}\r\n\r\n.sub-nav-btn:hover {\r\n  background: rgba(255, 255, 255, 0.1);\r\n  backdrop-filter: blur(20px) saturate(160%);\r\n  -webkit-backdrop-filter: blur(20px) saturate(160%);\r\n  color: white;\r\n}\r\n\r\n.sub-nav-btn:hover::before {\r\n  opacity: 0.5;\r\n  transform: scaleX(1);\r\n}\r\n\r\n.sub-nav-btn.active {\r\n  background: rgba(255, 255, 255, 0.15);\r\n  backdrop-filter: blur(25px) saturate(170%);\r\n  -webkit-backdrop-filter: blur(25px) saturate(170%);\r\n  color: white;\r\n  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);\r\n  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.1);\r\n}\r\n\r\n.sub-nav-btn.active::before {\r\n  opacity: 1;\r\n  transform: scaleX(1);\r\n  box-shadow: 0 0 15px var(--theme-highlight-glow);\r\n}\r\n\r\n/* Button separators */\r\n.sub-nav-btn:not(:last-child)::after {\r\n  content: '|';\r\n  position: absolute;\r\n  right: 0;\r\n  top: 50%;\r\n  transform: translateY(-50%);\r\n  color: rgba(255, 255, 255, 0.25);\r\n  font-weight: 300;\r\n  font-size: 18px;\r\n  pointer-events: none;\r\n  width: 1px;\r\n  height: 100%;\r\n}\r\n\r\n/* \r\n   PAGE-SPECIFIC STYLES HAVE BEEN EXTRACTED TO SEPARATE FILES:\r\n   - styles/pages/home.css: Home page sections (hero, features, cta, agents)\r\n   - styles/pages/services.css: Services grid and cards\r\n   - styles/pages/about.css: About content and team\r\n   - styles/pages/contact.css: Contact form and info\r\n   - styles/auth.css: Auth login forms\r\n   - styles/dashboards.css: Dashboard layouts\r\n   \r\n   This app.css file now contains ONLY base/global styles.\r\n   See index.html for CSS module links.\r\n*/"],"sourceRoot":""}]);
