    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.dashboard-section .stat-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 4px;
    opacity: 0;
    transition: opacity 0.3s ease;
}

.dashboard-section .stat-card.stat-primary::before {
