    height: 10px;
    border-radius: 50%;
    box-shadow: 0 0 0 4px rgba(0, 0, 0, 0.04);
}

.donut-key .dot.active {
    background: #38ef7d;
    box-shadow: 0 0 0 4px rgba(56, 239, 125, 0.18);
}

.donut-key .dot.renew {
    background: #f8c547;
    box-shadow: 0 0 0 4px rgba(248, 197, 71, 0.18);
}

.donut-key .dot.risk {
    background: #f5576c;
    box-shadow: 0 0 0 4px rgba(245, 87, 108, 0.18);
