.calendar-card .cal-day {
    padding: 8px 4px;
    border-radius: 8px;
    background: rgba(255, 255, 255, 0.12);
    text-align: center;
    font-weight: 500;
    color: var(--theme-text-primary);
    border: 1px solid rgba(255, 255, 255, 0.12);
    position: relative;
    cursor: pointer;
    transition: all 0.2s ease;
}

.calendar-card .cal-day:hover {
    background: rgba(255, 255, 255, 0.18);
    border-color: rgba(255, 255, 255, 0.2);
}

.calendar-card .cal-day.today {
    background: rgba(76, 175, 80, 0.2);
    border-color: rgba(76, 175, 80, 0.4);
    font-weight: 700;
    color: #4CAF50;
}

.calendar-card .cal-day.has-payment {
    background: rgba(33, 150, 243, 0.2);
    border-color: rgba(33, 150, 243, 0.4);
}

.calendar-card .cal-day.muted {
    opacity: 0.5;
    cursor: default;
}

.calendar-card .payment-count {
    display: inline-block;
    position: absolute;
    top: 0;
    right: 0;
    width: 16px;
    height: 16px;
    background: #FF6B6B;
    color: white;
    border-radius: 50%;
    font-size: 10px;
    font-weight: 700;
    line-height: 16px;
}

.calendar-card .upcoming-payments {
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin-top: 12px;
    padding-top: 12px;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.calendar-card .payment-slot {
    display: grid;
    grid-template-columns: 70px 1fr 80px;
    gap: 8px;
    align-items: center;
    padding: 10px 12px;
    border-radius: 8px;
    background: rgba(255, 255, 255, 0.08);
    border: 1px solid rgba(255, 255, 255, 0.12);
    color: var(--theme-text-primary);
    cursor: pointer;
    transition: all 0.2s ease;
    font-size: 13px;
}

.calendar-card .payment-slot:hover {
    background: rgba(255, 255, 255, 0.14);
    transform: translateX(2px);
}

.calendar-card .payment-slot.muted {
    opacity: 0.6;
    cursor: default;
}

.calendar-card .payment-slot.status-pending {
    border-left: 3px solid #FF9800;
}

.calendar-card .payment-slot.status-completed {
    border-left: 3px solid #4CAF50;
}

.calendar-card .payment-slot.status-failed {
    border-left: 3px solid #F44336;
}

.calendar-card .payment-date {
    font-weight: 600;
    text-transform: uppercase;
    font-size: 11px;
    color: var(--theme-text-secondary);
}

.calendar-card .payment-policy {
    font-size: 12px;
    color: var(--theme-text-primary);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.calendar-card .payment-amount {
    text-align: right;
    font-weight: 700;
    color: var(--theme-accent-color);
}

.calendar-card .calendar-error {
    padding: 20px;
    text-align: center;
    color: var(--theme-text-secondary);
    font-size: 13px;
}

.cal-slot {
    grid-column: span 5;
    padding: 10px 12px;
    border-radius: 12px;
    background: rgba(255, 255, 255, 0.08);
    border: 1px solid rgba(255, 255, 255, 0.12);
    color: var(--theme-text-primary);
}

.cal-slot.accent {
    background: rgba(118, 75, 162, 0.12);
    border-color: rgba(118, 75, 162, 0.24);
}

.chart-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.chart-title {
    font-size: 15px;
    font-weight: 700;
    color: var(--theme-text-primary);
    margin: 0;
    display: inline-flex;
    align-items: center;
    gap: 6px;
}

.chart-legend {
    display: flex;
    align-items: center;
    gap: 10px;
}

.chart-legend-item {
    display: inline-flex;
    align-items: center;
    gap: 6px;
    font-size: 14px;
    color: var(--theme-text-secondary);
}

.chart-legend-dot {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: var(--theme-accent-color);
    box-shadow: 0 0 0 4px rgba(155, 89, 182, 0.12);
}

.chart-body {
    position: relative;
    margin-top: 6px;
    min-height: 140px;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.6), rgba(255, 255, 255, 0.28));
    border-radius: 12px;
    border: 1px solid rgba(255, 255, 255, 0.2);
    overflow: hidden;
}

.sparkline-placeholder {
    position: absolute;
    inset: 0;
    background: linear-gradient(135deg, rgba(139, 35, 72, 0.14), rgba(155, 89, 182, 0.12));
    opacity: 0.55;
}

.chart-metrics {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
    gap: 10px;
    margin-top: 12px;
}

.chart-metric {
    background: rgba(255, 255, 255, 0.82);
    border: 1px solid rgba(255, 255, 255, 0.22);
    border-radius: 10px;
    padding: 10px 12px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    box-shadow: 0 8px 22px rgba(139, 35, 72, 0.10);
}

.chart-metric .label {
    font-size: 14px;
    color: var(--theme-text-secondary);
}

.chart-metric .value {
    font-size: 16px;
    font-weight: 700;
    color: var(--theme-text-primary);
}

.list-card .mini-table {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.mini-table-row {
    display: grid;
    grid-template-columns: 1.2fr 0.9fr 0.7fr 0.7fr 0.9fr;
    align-items: center;
    gap: 10px;
    padding: 12px 10px;
    border-radius: 12px;
    background: rgba(255, 255, 255, 0.76);
    border: 1px solid rgba(0, 0, 0, 0.04);
    box-shadow: 0 10px 26px rgba(0, 0, 0, 0.06);
}

.mini-table-head {
    background: rgba(255, 255, 255, 0.54);
    border: 1px solid rgba(0, 0, 0, 0.04);
    font-weight: 700;
    color: var(--theme-text-secondary);
    text-transform: uppercase;
    font-size: 12px;
    letter-spacing: 0.08em;
}

.mini-table-row span {
    color: var(--theme-text-primary);
    font-size: 14px;
}

.mini-table-row .cell-strong {
    font-weight: 700;
}

.chip {
    display: inline-flex;
    align-items: center;
    padding: 4px 10px;
    border-radius: 999px;
    font-weight: 700;
    font-size: 12px;
    border: 1px solid transparent;
}

.chip-low {
    background: rgba(56, 239, 125, 0.14);
    color: #1c7d4f;
    border-color: rgba(56, 239, 125, 0.28);
}

.chip-medium {
    background: rgba(248, 197, 71, 0.16);
    color: #8a5b00;
    border-color: rgba(248, 197, 71, 0.28);
}

.chip-high {
    background: rgba(245, 87, 108, 0.16);
    color: #982b3a;
    border-color: rgba(245, 87, 108, 0.3);
}

.contact-chips {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
}

.contact-chip {
    border: 1px solid rgba(0, 0, 0, 0.06);
    background: linear-gradient(145deg, rgba(118, 75, 162, 0.08), rgba(102, 126, 234, 0.08));
    color: var(--theme-text-primary);
    padding: 8px 12px;
    border-radius: 999px;
    font-weight: 700;
    font-size: 13px;
    cursor: pointer;
    transition: all 0.2s ease;
    box-shadow: 0 10px 26px rgba(0, 0, 0, 0.06);
}

.contact-chip:hover,
.contact-chip:focus-visible {
    outline: none;
    border-color: var(--theme-accent-color);
    box-shadow: 0 12px 30px rgba(118, 75, 162, 0.16);
    transform: translateY(-1px);
}

.contact-chip:active {
    transform: translateY(0);
}

.status-dot {
    display: inline-flex;
    align-items: center;
    gap: 6px;
    font-weight: 600;
}

.status-dot::before {
    content: '';
    width: 10px;
    height: 10px;
    border-radius: 50%;
    box-shadow: 0 0 0 4px rgba(0, 0, 0, 0.06);
}

.status-dot.ok::before {
    background: #38ef7d;
    box-shadow: 0 0 0 4px rgba(56, 239, 125, 0.14);
}

.status-dot.warn::before {
    background: #f8c547;
    box-shadow: 0 0 0 4px rgba(248, 197, 71, 0.18);
}

.status-dot.risk::before {
    background: #f5576c;
    box-shadow: 0 0 0 4px rgba(245, 87, 108, 0.16);
}

.icon-animated {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 28px;
    height: 28px;
    margin-right: 4px;
    color: var(--theme-primary-color);
}

.icon-animated svg {
    stroke-linecap: round;
    stroke-linejoin: round;
    overflow: visible;
}

.icon-animated svg * {
    stroke-dasharray: 120;
    stroke-dashoffset: 120;
    animation: line-draw 1.2s ease forwards;
    animation-delay: 0.05s;
}

@keyframes line-draw {
    to {
        stroke-dashoffset: 0;
    }
}

.dashboard-card {
    background: linear-gradient(160deg, rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.78));
    border: 1px solid rgba(255, 255, 255, 0.6);
    border-radius: 16px;
    padding: var(--dashboard-card-padding);
    box-shadow: 0 14px 34px rgba(31, 38, 135, 0.12), var(--shadow-sm);
    backdrop-filter: blur(12px) saturate(1.08);
    -webkit-backdrop-filter: blur(12px) saturate(1.08);
    margin-bottom: 12px;
    color: var(--theme-text-primary);
    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1), opacity 0.2s ease;
}

.dashboard-card.scrolled-collapsed {
    transform: scale(0.98);
    opacity: 0.85;
}

