/* Simple card title when no .card-header is used (sidebar cards) */
.dashboard-card>h3 {
    font-family: 'Cinzel', serif;
    font-size: 1.25rem;
    color: var(--theme-text-primary);
    margin: 0 0 16px 0;
}

.card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 12px;
    margin-bottom: var(--dashboard-card-header-margin);
    padding-bottom: var(--dashboard-card-header-padding);
    border-bottom: 2px solid var(--theme-surface-border);
}

.card-header h3 {
    font-family: 'Cinzel', serif;
    font-size: 1.25rem;
    color: var(--theme-text-primary);
    margin: 0;
}

/* Lists */
.policies-list,
.quotes-list,
.documents-list,
.tasks-list,
.alert-list,
.activity-list,
.prelaunch-checklist {
    display: flex;
    flex-direction: column;
    gap: 14px;
}

.task-item,
.alert-item,
.activity-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 12px;
    padding: 14px 16px;
    background: var(--acrylic-bg-light);
    border: 1px solid var(--theme-accent-border-subtle);
    border-radius: 12px;
    color: var(--theme-text-primary);
}

.task-title,
.alert-title,
.activity-title {
    margin: 0 0 4px 0;
    font-weight: 700;
}

.task-meta,
.alert-meta,
.activity-meta {
    margin: 0;
    color: var(--theme-text-secondary);
    font-size: 0.9rem;
}

.quick-actions {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

/* Skeleton loading states for dashboards */
.skeleton-mode .dashboard-stat-card,
.skeleton-mode .task-item,
.skeleton-mode .alert-item,
.skeleton-mode .activity-item {
    position: relative;
    overflow: hidden;
    color: transparent !important;
}

.skeleton-mode .dashboard-stat-card::after,
.skeleton-mode .task-item::after,
.skeleton-mode .alert-item::after,
.skeleton-mode .activity-item::after {
    content: '';
    position: absolute;
    inset: 0;
    background: linear-gradient(90deg,
            rgba(255, 255, 255, 0.04) 0%,
            rgba(255, 255, 255, 0.08) 40%,
            rgba(255, 255, 255, 0.04) 80%);
    animation: skeleton-shimmer 1.2s infinite;
}

.skeleton-mode .dashboard-stat-card .stat-icon,
.skeleton-mode .dashboard-stat-card .stat-value,
.skeleton-mode .dashboard-stat-card .stat-label,
.skeleton-mode .task-item *,
.skeleton-mode .alert-item *,
.skeleton-mode .activity-item * {
    color: transparent !important;
}

@keyframes skeleton-shimmer {
    0% {
        transform: translateX(-60%);
    }

    100% {
        transform: translateX(60%);
    }
}

.badge-light {
    background: rgba(255, 255, 255, 0.12);
    color: var(--theme-text-primary);
    border: 1px solid var(--theme-surface-border);
}

.prelaunch-checklist {
    padding-left: 18px;
    margin: 0;
    color: var(--theme-text-secondary);
}

.prelaunch-checklist li {
    margin-bottom: 8px;
}

.policy-item,
.quote-item {
    display: flex;
    align-items: center;
    gap: 16px;
    padding: 18px;
    background: rgba(255, 255, 255, 0.92);
    border: 1px solid rgba(255, 255, 255, 0.26);
    border-radius: 12px;
    transition: transform 0.2s ease, background 0.2s ease, border-color 0.2s ease, box-shadow 0.2s ease;
}

.policy-item:hover,
.quote-item:hover {
    background: var(--theme-accent-bg-soft);
    border-color: var(--theme-accent-border);
    transform: translateX(3px);
    box-shadow: var(--shadow);
}

.policy-icon {
    font-size: 2rem;
    width: 52px;
    height: 52px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--acrylic-bg-light);
    border: 1px solid var(--theme-surface-border);
    border-radius: 12px;
    flex-shrink: 0;
}

.policy-info,
.quote-info {
    flex: 1;
    min-width: 0;
}

.policy-actions {
    display: flex;
    align-items: center;
    gap: 10px;
    flex-shrink: 0;
}

.policy-info h4,
.quote-info h4 {
    font-size: 1.05rem;
    color: var(--theme-text-primary);
    margin: 0 0 6px 0;
}

.policy-info p,
.quote-info p {
    color: var(--theme-text-secondary);
    font-size: 0.92rem;
    margin: 0 0 10px 0;
}

.policy-meta {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 10px;
    font-size: 0.85rem;
    color: var(--theme-text-secondary);
}

.quote-date {
    font-size: 0.85rem;
    color: var(--theme-text-secondary);
}

.recent-docs-list {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.doc-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 12px;
    padding: 12px 14px;
    background: rgba(255, 255, 255, 0.82);
    border: 1px solid rgba(0, 0, 0, 0.06);
    border-radius: 12px;
    box-shadow: 0 12px 30px rgba(0, 0, 0, 0.08);
}

.doc-meta {
    display: flex;
    flex-direction: column;
    gap: 4px;
}

.doc-meta strong {
    font-size: 14px;
    color: var(--theme-text-primary);
}

.doc-meta span {
    color: var(--theme-text-secondary);
    font-size: 12px;
}

.doc-actions {
    display: inline-flex;
    align-items: center;
    gap: 8px;
}

.contact-modal {
    position: fixed;
    inset: 0;
    display: none;
    align-items: center;
    justify-content: center;
    z-index: 200;
}

.contact-modal.is-open {
    display: flex;
}

.modal-backdrop {
    position: absolute;
    inset: 0;
    background: rgba(0, 0, 0, 0.35);
    backdrop-filter: blur(6px);
    -webkit-backdrop-filter: blur(6px);
}

.modal-card {
    position: relative;
    width: min(420px, 92vw);
    background: linear-gradient(160deg, rgba(255, 255, 255, 0.95), rgba(255, 255, 255, 0.88));
    border: 1px solid rgba(0, 0, 0, 0.06);
    border-radius: 16px;
    padding: 16px;
    box-shadow: 0 20px 48px rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(10px) saturate(1.05);
    -webkit-backdrop-filter: blur(10px) saturate(1.05);
}

.modal-card-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 12px;
    margin-bottom: 10px;
}

.modal-card-body {
    display: flex;
    flex-direction: column;
    gap: 12px;
    margin-bottom: 12px;
}

.contact-detail {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    padding: 10px 12px;
    background: rgba(255, 255, 255, 0.7);
    border: 1px solid rgba(0, 0, 0, 0.04);
    border-radius: 12px;
}

.contact-detail span {
    color: var(--theme-text-secondary);
    font-size: 13px;
}

.contact-detail strong {
    color: var(--theme-text-primary);
}

.modal-card-actions {
    display: flex;
    justify-content: flex-end;
    gap: 10px;
}

/* Badges */
.badge {
    padding: 4px 12px;
    border-radius: 999px;
    font-size: 0.72rem;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.badge-success {
    background: var(--success);
    color: var(--theme-on-success);
}

.badge-warning {
    background: var(--warning);
    color: var(--theme-on-warning);
}

.badge-danger {
    background: var(--danger);
    color: var(--theme-on-danger);
}

.badge-primary {
    background: var(--theme-primary-color);
    color: var(--theme-highlight-light);
}

.badge-accent {
    background: var(--theme-accent-bg-soft);
    color: var(--theme-accent-color);
    border: 1px solid var(--theme-accent-border);
}

/* Tables */
.payments-table table,
.clients-table table {
    width: 100%;
    border-collapse: collapse;
}

.payments-table th,
.payments-table td,
.clients-table th,
.clients-table td {
    padding: 14px 12px;
    text-align: left;
    border-bottom: 1px solid var(--theme-surface-border);
}

.payments-table th,
.clients-table th {
    font-weight: 700;
    color: var(--theme-text-primary);
    background: var(--theme-navlink-active-bg);
    font-size: 0.9rem;
}

.payments-table td,
.clients-table td {
    color: var(--theme-text-secondary);
    font-size: 0.95rem;
}

.client-cell {
    display: flex;
    align-items: center;
    gap: 12px;
}

.client-avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: linear-gradient(135deg, var(--theme-primary-color), var(--theme-accent-color));
    color: var(--white);
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 800;
    font-size: 0.9rem;
    flex-shrink: 0;
}

.client-name {
    font-weight: 700;
    color: var(--theme-text-primary);
}

.client-email {
    font-size: 0.85rem;
    color: var(--theme-text-secondary);
}

/* Sidebar */
.dashboard-sidebar .dashboard-card {
    position: sticky;
    top: calc(var(--nav-height) + 14px);
}

.quick-actions {
    display: flex;
    flex-direction: column;
    gap: var(--dashboard-quick-actions-gap);
}

.agent-info {
    text-align: center;
}

.agent-avatar {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background: linear-gradient(135deg, var(--theme-primary-color), var(--theme-accent-color));
    color: var(--white);
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Cinzel', serif;
    font-size: 2rem;
    font-weight: 800;
    margin: 0 auto 16px;
    box-shadow: 0 10px 26px var(--theme-primary-shadow-color);
}

.agent-info h4 {
    font-family: 'Cinzel', serif;
    font-size: 1.15rem;
    margin: 0 0 6px 0;
    color: var(--theme-text-primary);
}

.agent-info p {
    color: var(--theme-text-secondary);
    font-size: 0.92rem;
    margin: 0 0 14px 0;
}

.agent-contact {
    margin: 14px 0;
    padding: 14px;
    background: var(--theme-navlink-active-bg);
    border: 1px solid var(--theme-surface-border);
    border-radius: 12px;
    text-align: left;
}

.agent-contact p {
    margin: 6px 0;
    font-size: 0.92rem;
    color: var(--theme-text-secondary);
}

/* Shared stat grid + cards */
.dashboard-stats {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(var(--dashboard-stat-min), 1fr));
    gap: var(--dashboard-stat-gap, var(--dashboard-stat-gap-shared));
    margin-top: 0;
    margin-bottom: 12px;
}

.dashboard-stat-card {
    background: linear-gradient(160deg, rgba(255, 255, 255, 0.09), rgba(255, 255, 255, 0.05));
    backdrop-filter: blur(calc(var(--acrylic-blur-strong) * 1.05)) saturate(calc(var(--acrylic-saturate-strong) * 1.05));
    -webkit-backdrop-filter: blur(calc(var(--acrylic-blur-strong) * 1.05)) saturate(calc(var(--acrylic-saturate-strong) * 1.05));
    padding: var(--dashboard-stat-padding);
    border-radius: var(--dashboard-stat-radius);
    border: 1px solid var(--theme-accent-border);
    box-shadow: var(--dashboard-stat-shadow, var(--dashboard-stat-shadow-shared));
    transition: var(--dashboard-stat-transition, var(--dashboard-stat-transition-shared));
    display: flex;
    align-items: center;
    gap: var(--dashboard-stat-gap, var(--dashboard-stat-gap-shared));
    position: relative;
    overflow: hidden;
    transition: transform 0.42s cubic-bezier(0.33, 1, 0.68, 1), box-shadow 0.42s cubic-bezier(0.33, 1, 0.68, 1), background 0.42s cubic-bezier(0.33, 1, 0.68, 1), border-color 0.42s cubic-bezier(0.33, 1, 0.68, 1);
}

.dashboard-stat-card:hover {
    transform: translateY(var(--dashboard-stat-hover-lift, var(--dashboard-stat-hover-lift-shared)));
    background: linear-gradient(160deg, rgba(255, 255, 255, 0.14), rgba(255, 255, 255, 0.07));
    backdrop-filter: blur(calc(var(--acrylic-blur-strong) * 1.2)) saturate(calc(var(--acrylic-saturate-strong) * 1.15));
    -webkit-backdrop-filter: blur(calc(var(--acrylic-blur-strong) * 1.2)) saturate(calc(var(--acrylic-saturate-strong) * 1.15));
    box-shadow: var(--dashboard-stat-hover-shadow, var(--dashboard-stat-hover-shadow-shared));
}

.dashboard-stat-card .stat-icon {
    font-size: var(--dashboard-stat-icon-font, var(--dashboard-stat-icon-font-shared));
    width: var(--dashboard-stat-icon-size, var(--dashboard-stat-icon-size-shared));
    height: var(--dashboard-stat-icon-size, var(--dashboard-stat-icon-size-shared));
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.16), rgba(255, 255, 255, 0.06));
    border-radius: var(--dashboard-stat-icon-radius, var(--dashboard-stat-icon-radius-shared));
    border: 1px solid rgba(255, 255, 255, 0.28);
    backdrop-filter: blur(calc(var(--acrylic-blur) * 1.05)) saturate(calc(var(--acrylic-saturate) * 1.18));
    -webkit-backdrop-filter: blur(calc(var(--acrylic-blur) * 1.05)) saturate(calc(var(--acrylic-saturate) * 1.18));
    box-shadow: 0 10px 28px rgba(139, 35, 72, 0.20), inset 0 1px 0 rgba(255, 255, 255, 0.26);
    flex-shrink: 0;
}

.stat-info {
    flex: 1;
}

.stat-value {
    font-family: 'Cinzel', serif;
    font-size: 2rem;
    font-weight: 700;
    color: var(--theme-text-primary);
    line-height: 1;
    margin-bottom: 6px;
}

.stat-label {
    font-size: 0.95rem;
    color: var(--theme-text-secondary);
    font-weight: 500;
}

.document-link {
    padding: 12px 14px;
    background: var(--theme-navlink-active-bg);
    border: 1px solid var(--theme-surface-border);
    border-radius: 10px;
    text-decoration: none;
    color: var(--theme-text-primary);
    font-size: 0.92rem;
    transition: transform 0.2s ease, background 0.2s ease;
    display: flex;
    align-items: center;
    gap: 10px;
}

.document-link:hover {
    background: var(--theme-accent-bg-soft);
    border-color: var(--theme-accent-border);
    transform: translateX(3px);
}

.task-item {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 12px;
    background: rgba(255, 255, 255, 0.94);
    border: 1px solid rgba(255, 255, 255, 0.26);
    border-radius: 10px;
    transition: background 0.2s ease, border-color 0.2s ease, transform 0.2s ease;
}

.task-item:hover {
    background: var(--theme-accent-bg-soft);
    border-color: var(--theme-accent-border);
    transform: translateX(2px);
}

.task-item input[type="checkbox"] {
    width: 18px;
    height: 18px;
    cursor: pointer;
}

.commissions-info {
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.commission-row {
    display: flex;
    justify-content: space-between;
    gap: 12px;
    padding: 10px 0;
    border-bottom: 1px solid var(--theme-surface-border);
    color: var(--theme-text-secondary);
}

.commission-row strong {
    color: var(--theme-text-primary);
}

/* Responsive */
@media (max-width: 1480px) {
    .dashboard-grid {
        grid-template-columns: repeat(2, minmax(300px, 1fr));
    }

    .dashboard-main {
        grid-column: span 2;
        grid-template-columns: repeat(2, minmax(var(--dashboard-main-col-min), 1fr));
    }

    .dashboard-sidebar {
        grid-column: span 2;
    }
}

@media (max-width: 1100px) {
    .dashboard-grid {
        grid-template-columns: 1fr;
    }

    .dashboard-stage.with-rail {
        grid-template-columns: 1fr;
    }

    .dashboard-main {
        grid-column: span 1;
        grid-template-columns: 1fr;
    }

    .dashboard-sidebar {
        grid-column: span 1;
    }

    .dashboard-sidebar .dashboard-card {
        position: static;
    }

    .mimic-col.main {
        grid-template-columns: repeat(2, minmax(0, 1fr));
    }

    .mimic-col.main>.content-card,
    .mimic-col.main>.chart-card,
    .mimic-col.main>.subgrid {
        grid-column: span 1;
    }

    .mimic-col.main>.list-card,
    .mimic-col.main>.subgrid.two {
        grid-column: span 2;
    }

    .dashboard-body.with-rail {
        grid-template-columns: 1fr;
    }

    .dashboard-rail.right-rail {
        position: static;
        order: 2;
    }

    .hero-surface .hero-grid {
        grid-template-columns: 1fr;
        gap: 12px;
    }
}

@media (max-width: 560px) {
    .dashboard-card {
        padding: 20px;
    }

    .mimic-grid {
        grid-template-columns: 1fr;
    }

    .mimic-col.rail {
        grid-column: 1;
    }

    .policy-item,
    .quote-item {
        flex-direction: column;
        align-items: flex-start;
    }

    .mimic-grid {
        grid-template-columns: 1fr;
    }

    .mimic-col.rail,
    .mimic-col.charts,
    .mimic-col.main {
        grid-column: 1;
    }

    .mimic-col.main {
        grid-template-columns: 1fr;
    }

    .mimic-col.main>.content-card,
    .mimic-col.main>.chart-card,
    .mimic-col.main>.subgrid,
    .mimic-col.main>.list-card,
    .mimic-col.main>.subgrid.two {
        grid-column: span 1;
    }

    .dashboard-rail.right-rail {
        position: static;
        order: 2;
    }
}

