;IAClB,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,cAAc;AAClB;;AAEA;IACI,oCAAoC;IACpC,cAAc;AAClB;;AAEA;IACI,oCAAoC;IACpC,cAAc;AAClB;;AAEA;IACI,oCAAoC;IACpC,cAAc;AAClB;;AAEA;IACI,OAAO;AACX;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,QAAQ;IACR,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,gCAAgC;IAChC,gBAAgB;AACpB;;AAEA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,oCAAoC;IACpC,cAAc;AAClB;;AAEA;IACI,oCAAoC;IACpC,cAAc;AAClB;;AAEA;IACI,oCAAoC;IACpC,cAAc;AAClB;;AAEA;IACI,kBAAkB;IAClB,kCAAkC;IAClC,aAAa;IACb,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,SAAS;IACT,iBAAiB;IACjB,kCAAkC;IAClC,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,gBAAgB;IAChB,gCAAgC;AACpC;;AAEA,0EAA0E;AAC1E;IACI,gCAAgC;IAChC,0CAA0C;IAC1C,+CAA+C;IAC/C,2CAA2C;IAC3C,gCAAgC;AACpC;;AAEA;IACI,kCAAkC;IAClC,sCAAsC;AAC1C;;AAEA;;;IAGI,sBAAsB;AAC1B;;AAEA;;IAEI,oCAAoC;IACpC,cAAc;AAClB;;AAEA;;IAEI,oCAAoC;IACpC,cAAc;AAClB;;AAEA;;IAEI,oCAAoC;IACpC,cAAc;AAClB;;AAEA,mDAAmD;AACnD;IACI,gDAAgD;IAChD,8BAA8B;AAClC;;AAEA,iDAAiD;AACjD;IACI,mBAAmB;AACvB;;AAEA;IACI,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,qCAAqC;IACrC,2BAA2B;IAC3B,mBAAmB;IACnB,aAAa;IACb,qCAAqC;AACzC;;AAEA;IACI,mBAAmB;AACvB;;AAEA,+CAA+C;;AAE/C,sCAAsC;AACtC;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,eAAe;IACf,yBAAyB;AAC7B;;AAEA;IACI,2BAA2B;IAC3B,0CAA0C;AAC9C;;AAEA,yBAAyB;AACzB;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,qCAAqC;IACrC,2BAA2B;IAC3B,mBAAmB;IACnB,aAAa;IACb,qCAAqC;AACzC;;AAEA;;wEAEwE;;AAExE,8BAA8B","sourcesContent":["/* Shared Dashboard UI (restored from backup, tokenized)\r\n   This file provides the missing layout/components for dashboards:\r\n   - grid layout\r\n   - cards + headers\r\n   - lists (policies/quotes)\r\n   - tables\r\n   - badges\r\n   - icon buttons\r\n   - sidebar widgets\r\n*/\r\n\r\n/* Import Font Manager for centralized typography & colors */\r\n@import './Font-Manager.css';\r\n\r\n:root {\r\n    /* Modern Dashboard Colors - Aligned with Brand Palette */\r\n    --sidebar-width: 300px;\r\n    --sidebar-slim-width: 80px;\r\n    --rail-width: 300px;\r\n    --dashboard-max-width: 1680px;\r\n    --dashboard-content-width: 90%;\r\n\r\n    /* Brand gradients */\r\n    --color-primary: linear-gradient(135deg, #8b2348 0%, #9b59b6 100%);\r\n    --color-success: linear-gradient(135deg, #28a745 0%, #20c997 100%);\r\n    --color-warning: linear-gradient(135deg, #ffc107 0%, #fd7e14 100%);\r\n    --color-info: linear-gradient(135deg, #17a2b8 0%, #20c997 100%);\r\n\r\n    /* Background & Surface Colors */\r\n    --bg-main: #f8f9fa;\r\n    --bg-card: #ffffff;\r\n    --bg-sidebar: #0d1117;\r\n    --bg-dashboard: linear-gradient(135deg, #f6f7fb 0%, #eef1f7 45%, #e7ebf3 100%);\r\n\r\n    /* Legacy compatibility */\r\n    --dashboard-bg-light: var(--bg-main);\r\n    --dashboard-stat-gap-shared: 14px;\r\n    --dashboard-stat-icon-size-shared: 60px;\r\n    --dashboard-stat-icon-radius-shared: 14px;\r\n    --dashboard-stat-transition-shared: all 0.42s cubic-bezier(0.33, 1, 0.68, 1);\r\n\r\n    /* Theme-adaptive variables for high contrast */\r\n    --dashboard-bg-surface: rgba(255, 255, 255, 0.92);\r\n    --dashboard-border: rgba(0, 0, 0, 0.06);\r\n    --dashboard-border-subtle: rgba(0, 0, 0, 0.08);\r\n    --dashboard-radius-lg: 18px;\r\n}\r\n\r\n/* Dark theme overrides */\r\nhtml[data-theme=\"dark-forest\"] {\r\n    --dashboard-bg-surface: rgba(20, 30, 25, 0.88);\r\n    --dashboard-border: rgba(255, 255, 255, 0.1);\r\n    --dashboard-border-subtle: rgba(255, 255, 255, 0.12);\r\n}\r\n\r\n/* Dashboard shell & layout */\r\n.dashboard-section {\r\n    background: var(--bg-dashboard);\r\n    height: 100vh;\r\n    width: 100vw;\r\n    margin: 0;\r\n    padding: 0;\r\n    /* SCROLL UNIVERSAL: overflow-y manejado por scroll-modal-fixes.css */\r\n    display: flex;\r\n    flex-direction: column;\r\n}\r\n\r\nhtml[data-theme=\"dark-forest\"] .dashboard-section {\r\n    background: #060b0a;\r\n}\r\n\r\n/* Hero Container - Full Width Outside Grid */\r\n.dashboard-section .dashboard-hero-container {\r\n    width: 100%;\r\n    flex-shrink: 0;\r\n    padding: 12px 12px 12px 12px;\r\n}\r\n\r\n.dashboard-section .dashboard-layout {\r\n    display: grid;\r\n    grid-template-columns: var(--sidebar-width) minmax(0, 1fr) var(--rail-width);\r\n    gap: 0;\r\n    flex: 1;\r\n    min-height: 0;\r\n    width: 100%;\r\n    max-width: 100vw;\r\n    margin: 0;\r\n    padding: 0;\r\n    /* SCROLL UNIVERSAL: overflow manejado por scroll-modal-fixes.css */\r\n}\r\n\r\n.dashboard-section .dashboard-main-wrapper {\r\n    /* SCROLL UNIVERSAL: height y overflow manejados por scroll-modal-fixes.css */\r\n    min-width: 0;\r\n    width: 100%;\r\n}\r\n\r\n.dashboard-shell {\r\n    width: 100%;\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\n.dashboard-section.no-sidebar .dashboard-layout {\r\n    display: block;\r\n}\r\n\r\n.dashboard-section.no-sidebar .dashboard-main-wrapper {\r\n    margin-left: 0;\r\n    padding: 0;\r\n    max-width: none;\r\n}\r\n\r\n/* Floating right rail */\r\n.dashboard-section.no-sidebar .dashboard-shell {\r\n    padding: 0 20px 36px;\r\n}\r\n\r\n/* ===== DASHBOARD HEADER - SHARED ACROSS ALL DASHBOARDS ===== */\r\n.dashboard-section .dashboard-header {\r\n    display: none !important;\r\n}\r\n\r\n/* Ocultar navbar global cuando se navega a dashboards (evita header doble) */\r\nbody[data-page=\"client-dashboard\"] .navbar,\r\nbody[data-page=\"agent-dashboard\"] .navbar,\r\nbody[data-page=\"admin-dashboard\"] .navbar,\r\nbody[data-page=\"client-dashboard\"] .navbar-header,\r\nbody[data-page=\"agent-dashboard\"] .navbar-header,\r\nbody[data-page=\"admin-dashboard\"] .navbar-header {\r\n    display: none !important;\r\n}\r\n\r\n.dashboard-section .dashboard-header>div:first-child h1 {\r\n    font-family: var(--dashboard-font-family-primary);\r\n    font-size: var(--dashboard-text-2xl);\r\n    color: var(--theme-accent-color);\r\n    margin: 0 0 8px 0;\r\n    font-weight: var(--dashboard-font-bold);\r\n    text-shadow: 0 2px 8px var(--theme-primary-shadow-color);\r\n}\r\n\r\n.dashboard-section .dashboard-header>div:first-child p {\r\n    color: var(--theme-text-secondary);\r\n    font-size: var(--dashboard-text-md);\r\n    margin: 0;\r\n}\r\n\r\n.dashboard-section .dashboard-header .btn {\r\n    padding: 12px 26px;\r\n    background: linear-gradient(140deg, rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.02));\r\n    color: var(--theme-text-primary);\r\n    border: 1px solid var(--theme-accent-border);\r\n    border-radius: 12px;\r\n    cursor: pointer;\r\n    font-weight: var(--dashboard-font-semibold);\r\n    backdrop-filter: blur(var(--acrylic-blur)) saturate(var(--acrylic-saturate-strong));\r\n    -webkit-backdrop-filter: blur(var(--acrylic-blur)) saturate(var(--acrylic-saturate-strong));\r\n    box-shadow: 0 12px 30px rgba(0, 0, 0, 0.22), inset 0 1px 0 rgba(255, 255, 255, 0.14);\r\n    transition: all 0.3s ease;\r\n}\r\n\r\n.dashboard-section .dashboard-header .btn:hover {\r\n    transform: translateY(-3px);\r\n    background: linear-gradient(150deg, rgba(255, 255, 255, 0.12), rgba(255, 255, 255, 0.04));\r\n    box-shadow: 0 16px 38px rgba(0, 0, 0, 0.3), 0 0 0 1px var(--theme-accent-border);\r\n}\r\n\r\n/* ===== END DASHBOARD HEADER ===== */\r\n\r\n/* Integrated content without additional rail wrapping */\r\n.dashboard-stage {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 12px;\r\n    /* SCROLL UNIVERSAL: overflow manejado por scroll-modal-fixes.css */\r\n    overflow: visible;\r\n    height: auto;\r\n    padding: 0 12px 12px 12px;\r\n}\r\n\r\n/* Scroll collapse behavior:\r\n   When user scrolls down past threshold (80px), hero-surface collapses.\r\n   Controlled by scrollCollapse.js which adds/removes COLLAPSE_CLASS.\r\n   Target: .dashboard-section (scroll universal)\r\n*/\r\n\r\n.stage-main {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 12px;\r\n}\r\n\r\n.dashboard-body {\r\n    display: block;\r\n    margin-top: 8px;\r\n}\r\n\r\n/* Slim Sidebar Navigation */\r\n.dashboard-section .dashboard-sidebar {\r\n    /* SCROLL UNIVERSAL: No sticky, no overflow */\r\n    width: var(--sidebar-width);\r\n    display: flex;\r\n    flex-direction: column;\r\n    padding: 0;\r\n}\r\n\r\n.dashboard-section .dashboard-sidebar .sidebar-surface {\r\n    /* SCROLL UNIVERSAL: height auto */\r\n    height: auto;\r\n}\r\n\r\n.sidebar-stack {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 12px;\r\n    padding: 12px;\r\n}\r\n\r\n.sidebar-block .sidebar-header,\r\n.sidebar-block .sidebar-quick-actions,\r\n.sidebar-block .sidebar-widget-group,\r\n.sidebar-block .sidebar-widget {\r\n    background: transparent;\r\n    border: 0;\r\n    box-shadow: none;\r\n    padding: 0;\r\n    margin: 0;\r\n}\r\n\r\n.sidebar-block .sidebar-qa-header {\r\n    margin-bottom: 8px;\r\n}\r\n\r\n.sidebar-block .qa-btn,\r\n.sidebar-block .client-pill,\r\n.sidebar-block .widget-mini-item,\r\n.sidebar-block .sidebar-logout {\r\n    width: 100%;\r\n}\r\n\r\n.sidebar-surface {\r\n    background: transparent;\r\n    border-radius: 20px;\r\n    border: 0;\r\n    box-shadow: none;\r\n    backdrop-filter: none;\r\n    -webkit-backdrop-filter: none;\r\n    padding: 0;\r\n    /* SCROLL UNIVERSAL: overflow manejado por scroll-modal-fixes.css */\r\n    overflow: visible;\r\n    height: auto;\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 0;\r\n}\r\n\r\nhtml[data-theme=\"dark-forest\"] .sidebar-surface,\r\nhtml[data-theme=\"dark-forest\"] .rail-surface {\r\n    background: transparent;\r\n    border: 0;\r\n    box-shadow: none;\r\n    color: var(--theme-text-primary);\r\n}\r\n\r\n.sidebar-block {\r\n    background: transparent;\r\n    border: 0;\r\n    box-shadow: none;\r\n    padding: 0;\r\n}\r\n\r\nhtml[data-theme=\"dark-forest\"] .sidebar-block {\r\n    background: transparent;\r\n    border: 0;\r\n    box-shadow: none;\r\n}\r\n\r\n.dashboard-section .sidebar-header {\r\n    padding: 14px 16px;\r\n    border-bottom: 1px solid rgba(15, 23, 42, 0.08);\r\n    text-align: center;\r\n    flex-shrink: 0;\r\n}\r\n\r\n.d