tween;\r\n    align-items: center;\r\n    margin-bottom: 10px;\r\n}\r\n\r\n.chart-title {\r\n    font-size: 15px;\r\n    font-weight: 700;\r\n    color: var(--theme-text-primary);\r\n    margin: 0;\r\n    display: inline-flex;\r\n    align-items: center;\r\n    gap: 6px;\r\n}\r\n\r\n.chart-legend {\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 10px;\r\n}\r\n\r\n.chart-legend-item {\r\n    display: inline-flex;\r\n    align-items: center;\r\n    gap: 6px;\r\n    font-size: 14px;\r\n    color: var(--theme-text-secondary);\r\n}\r\n\r\n.chart-legend-dot {\r\n    width: 10px;\r\n    height: 10px;\r\n    border-radius: 50%;\r\n    background: var(--theme-accent-color);\r\n    box-shadow: 0 0 0 4px rgba(155, 89, 182, 0.12);\r\n}\r\n\r\n.chart-body {\r\n    position: relative;\r\n    margin-top: 6px;\r\n    min-height: 140px;\r\n    background: linear-gradient(180deg, rgba(255, 255, 255, 0.6), rgba(255, 255, 255, 0.28));\r\n    border-radius: 12px;\r\n    border: 1px solid rgba(255, 255, 255, 0.2);\r\n    overflow: hidden;\r\n}\r\n\r\n.sparkline-placeholder {\r\n    position: absolute;\r\n    inset: 0;\r\n    background: linear-gradient(135deg, rgba(139, 35, 72, 0.14), rgba(155, 89, 182, 0.12));\r\n    opacity: 0.55;\r\n}\r\n\r\n.chart-metrics {\r\n    display: grid;\r\n    grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));\r\n    gap: 10px;\r\n    margin-top: 12px;\r\n}\r\n\r\n.chart-metric {\r\n    background: rgba(255, 255, 255, 0.82);\r\n    border: 1px solid rgba(255, 255, 255, 0.22);\r\n    border-radius: 10px;\r\n    padding: 10px 12px;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    gap: 10px;\r\n    box-shadow: 0 8px 22px rgba(139, 35, 72, 0.10);\r\n}\r\n\r\n.chart-metric .label {\r\n    font-size: 14px;\r\n    color: var(--theme-text-secondary);\r\n}\r\n\r\n.chart-metric .value {\r\n    font-size: 16px;\r\n    font-weight: 700;\r\n    color: var(--theme-text-primary);\r\n}\r\n\r\n.list-card 