  box-shadow: 0 10px 26px rgba(0, 0, 0, 0.06);\r\n}\r\n\r\n.contact-chip:hover,\r\n.contact-chip:focus-visible {\r\n    outline: none;\r\n    border-color: var(--theme-accent-color);\r\n    box-shadow: 0 12px 30px rgba(118, 75, 162, 0.16);\r\n    transform: translateY(-1px);\r\n}\r\n\r\n.contact-chip:active {\r\n    transform: translateY(0);\r\n}\r\n\r\n.status-dot {\r\n    display: inline-flex;\r\n    align-items: center;\r\n    gap: 6px;\r\n    font-weight: 600;\r\n}\r\n\r\n.status-dot::before {\r\n    content: '';\r\n    width: 10px;\r\n    height: 10px;\r\n    border-radius: 50%;\r\n    box-shadow: 0 0 0 4px rgba(0, 0, 0, 0.06);\r\n}\r\n\r\n.status-dot.ok::before {\r\n    background: #38ef7d;\r\n    box-shadow: 0 0 0 4px rgba(56, 239, 125, 0.14);\r\n}\r\n\r\n.status-dot.warn::before {\r\n    background: #f8c547;\r\n    box-shadow: 0 0 0 4px rgba(248, 197, 71, 0.18);\r\n}\r\n\r\n.status-dot.risk::before {\r\n    background: #f5576c;\r\n    box-shadow: 0 0 0 4px rgba(245, 87, 108, 0.16);\r\n}\r\n\r\n.icon-animated {\r\n    display: inline-flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    width: 28px;\r\n    height: 28px;\r\n    margin-right: 4px;\r\n    color: var(--theme-primary-color);\r\n}\r\n\r\n.icon-animated svg {\r\n    stroke-linecap: round;\r\n    stroke-linejoin: round;\r\n    overflow: visible;\r\n}\r\n\r\n.icon-animated svg * {\r\n    stroke-dasharray: 120;\r\n    stroke-dashoffset: 120;\r\n    animation: line-draw 1.2s ease forwards;\r\n    animation-delay: 0.05s;\r\n}\r\n\r\n@keyframes line-draw {\r\n    to {\r\n        stroke-dashoffset: 0;\r\n    }\r\n}\r\n\r\n.dashboard-card {\r\n    background: linear-gradient(160deg, rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.78));\r\n    border: 1px solid rgba(255, 255, 255, 0.6);\r\n    border-radius: 16px;\r\n    padding: var(--dashboard-card-padding);\r\n    box-shadow: 0 14px 34px rgba(31, 38, 135, 0.12), var(--shadow-sm);\r\n    backdrop-filter: blur(12px) saturate(1.08);\r\n    -webkit-backdrop-filter: blur(12px) saturate(1.08);\r\n    margin-bottom: 12px;\r\n    color: var(--theme-text-primary);\r\n    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1), opacity 0.2s ease;\r\n}\r\n\r\n.dashboard-card.scrolled-collapsed {\r\n    transform: scale(0.98);\r\n    opacity: 0.85;\r\n}\r\n\r\n/* Obsoleto - tabs antiguos ocultos usan .dashboard-card */\r\n/* .agent-dashboard .dashboard-card {\r\n    padding: 18px 16px;\r\n    border-radius: 14px;\r\n}\r\n\r\n.agent-dashboard .dashboard-stats {\r\n    gap: 12px;\r\n    margin-bottom: 12px;\r\n} */\r\n\r\n/* Simple card title when no .card-header is used (sidebar cards) */\r\n.dashboard-card>h3 {\r\n    font-family: 'Cinzel', serif;\r\n    font-size: 1.25rem;\r\n    color: var(--theme-text-primary);\r\n    margin: 0 0 16px 0;\r\n}\r\n\r\n.card-header {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    gap: 12px;\r\n    margin-bottom: var(--dashboard-card-header-margin);\r\n    padding-bottom: var(--dashboard-card-header-padding);\r\n    border-bottom: 2px solid var(--theme-surface-border);\r\n}\r\n\r\n.card-header h3 {\r\n    font-family: 'Cinzel', serif;\r\n    font-size: 1.25rem;\r\n    color: var(--theme-text-primary);\r\n    margin: 0;\r\n}\r\n\r\n/* Lists */\r\n.policies-list,\r\n.quotes-list,\r\n.documents-list,\r\n.tasks-list,\r\n.alert-list,\r\n.activity-list,\r\n.prelaunch-checklist {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 14px;\r\n}\r\n\r\n.task-item,\r\n.alert-item,\r\n.activity-item {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    gap: 12px;\r\n    padding: 14px 16px;\r\n    background: var(--acrylic-bg-light);\r\n    border: 1px solid var(--theme-accent-border-subtle);\r\n    border-radius: 12px;\r\n    color: var(--theme-text-primary);\r\n}\r\n\r\n.task-title,\r\n.alert-title,\r\n.activity-title {\r\n    margin: 0 0 4px 0;\r\n    font-weight: 700;\r\n}\r\n\r\n.task-meta,\r\n.alert-meta,\r\n.activity-meta {\r\n    margin: 0;\r\n    color: var(--theme-text-secondary);\r\n    font-size: 0.9rem;\r\n}\r\n\r\n.quick-actions {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 10px;\r\n}\r\n\r\n/* Skeleton loading states for dashboards */\r\n.skeleton-mode .dashboard-stat-card,\r\n.skeleton-mode .task-item,\r\n.skeleton-mode .alert-item,\r\n.skeleton-mode .activity-item {\r\n    position: relative;\r\n    overflow: hidden;\r\n    color: transparent !important;\r\n}\r\n\r\n.skeleton-mode .dashboard-stat-card::after,\r\n.skeleton-mode .task-item::after,\r\n.skeleton-mode .alert-item::after,\r\n.skeleton-mode .activity-item::after {\r\n    content: '';\r\n    position: absolute;\r\n    inset: 0;\r\n    background: linear-gradient(90deg,\r\n            rgba(255, 255, 255, 0.04) 0%,\r\n            rgba(255, 255, 255, 0.08) 40%,\r\n            rgba(255, 255, 255, 0.04) 80%);\r\n    animation: skeleton-shimmer 1.2s infinite;\r\n}\r\n\r\n.skeleton-mode .dashboard-stat-card .stat-icon,\r\n.skeleton-mode .dashboard-stat-card .stat-value,\r\n.skeleton-mode .dashboard-stat-card .stat-label,\r\n.skeleton-mode .task-item *,\r\n.skeleton-mode .alert-item *,\r\n.skeleton-mode .activity-item * {\r\n    color: transparent !important;\r\n}\r\n\r\n@keyframes skeleton-shimmer {\r\n    0% {\r\n        transform: translateX(-60%);\r\n    }\r\n\r\n    100% {\r\n        transform: translateX(60%);\r\n    }\r\n}\r\n\r\n.badge-light {\r\n    background: rgba(255, 255, 255, 0.12);\r\n    color: var(--theme-text-primary);\r\n    border: 1px solid var(--theme-surface-border);\r\n}\r\n\r\n.prelaunch-checklist {\r\n    padding-left: 18px;\r\n    margin: 0;\r\n    color: var(--theme-text-secondary);\r\n}\r\n\r\n.prelaunch-checklist li {\r\n    margin-bottom: 8px;\r\n}\r\n\r\n.policy-item,\r\n.quote-item {\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 16px;\r\n    padding: 18px;\r\n    background: rgba(255, 255, 255, 0.92);\r\n    border: 1px solid rgba(255, 255, 255, 0.26);\r\n    border-radius: 12px;\r\n    transition: transform 0.2s ease, background 0.2s ease, border-color 0.2s ease, box-shadow 0.2s ease;\r\n}\r\n\r\n.policy-item:hover,\r\n.quote-item:hover {\r\n    background: var(--theme-accent-bg-soft);\r\n    border-color: var(--theme-accent-border);\r\n    transform: translateX(3px);\r\n    box-shadow: var(--shadow);\r\n}\r\n\r\n.policy-icon {\r\n    font-size: 2rem;\r\n    width: 52px;\r\n    height: 52px;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    background: var(--acrylic-bg-light);\r\n    border: 1px solid var(--theme-surface-border);\r\n    border-radius: 12px;\r\n    flex-shrink: 0;\r\n}\r\n\r\n.policy-info,\r\n.quote-info {\r\n    flex: 1;\r\n    min-width: 0;\r\n}\r\n\r\n.policy-actions {\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 10px;\r\n    flex-shrink: 0;\r\n}\r\n\r\n.policy-info h4,\r\n.quote-info h4 {\r\n    font-size: 1.05rem;\r\n    color: var(--theme-text-primary);\r\n    margin: 0 0 6px 0;\r\n}\r\n\r\n.policy-info p,\r\n.quote-info p {\r\n    color: var(--theme-text-secondary);\r\n    font-size: 0.92rem;\r\n    margin: 0 0 10px 0;\r\n}\r\n\r\n.policy-meta {\r\n    display: flex;\r\n    align-items: center;\r\n    flex-wrap: wrap;\r\n    gap: 10px;\r\n    font-size: 0.85rem;\r\n    color: var(--theme-text-secondary);\r\n}\r\n\r\n.quote-date {\r\n    font-size: 0.85rem;\r\n    color: var(--theme-text-secondary);\r\n}\r\n\r\n.recent-docs-list {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 10px;\r\n}\r\n\r\n.doc-item {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    gap: 12px;\r\n    padding: 12px 14px;\r\n    background: rgba(255, 255, 255, 0.82);\r\n    border: 1px solid rgba(0, 0, 0, 0.06);\r\n    border-radius: 12px;\r\n    box-shadow: 0 12px 30px rgba(0, 0, 0, 0.08);\r\n}\r\n\r\n.doc-meta {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 4px;\r\n}\r\n\r\n.doc-meta strong {\r\n    font-size: 14px;\r\n    color: var(--theme-text-primary);\r\n}\r\n\r\n.doc-meta span {\r\n    color: var(--theme-text-secondary);\r\n    font-size: 12px;\r\n}\r\n\r\n.doc-actions {\r\n    display: inline-flex;\r\n    align-items: center;\r\n    gap: 8px;\r\n}\r\n\r\n.contact-modal {\r\n    position: fixed;\r\n    inset: 0;\r\n    display: none;\r\n    align-items: center;\r\n    justify-content: center;\r\n    z-index: 200;\r\n}\r\n\r\n.contact-modal.is-open {\r\n    display: flex;\r\n}\r\n\r\n.modal-backdrop {\r\n    position: absolute;\r\n    inset: 0;\r\n    background: rgba(0, 0, 0, 0.35);\r\n    backdrop-filter: blur(6px);\r\n    -webkit-backdrop-filter: blur(6px);\r\n}\r\n\r\n.modal-card {\r\n    position: relative;\r\n    width: min(420px, 92vw);\r\n    background: linear-gradient(160deg, rgba(255, 255, 255, 0.95), rgba(255, 255, 255, 0.88));\r\n    border: 1px solid rgba(0, 0, 0, 0.06);\r\n    border-radius: 16px;\r\n    padding: 16px;\r\n    box-shadow: 0 20px 48px rgba(0, 0, 0, 0.2);\r\n    backdrop-filter: blur(10px) saturate(1.05);\r\n    -webkit-backdrop-filter: blur(10px) saturate(1.05);\r\n}\r\n\r\n.modal-card-header {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    gap: 12px;\r\n    margin-bottom: 10px;\r\n}\r\n\r\n.modal-card-body {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 12px;\r\n    margin-bottom: 12px;\r\n}\r\n\r\n.contact-detail {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    gap: 10px;\r\n    padding: 10px 12px;\r\n    background: rgba(255, 255, 255, 0.7);\r\n    border: 1px solid rgba(0, 0, 0, 0.04);\r\n    border-radius: 12px;\r\n}\r\n\r\n.contact-detail span {\r\n    color: var(--theme-text-secondary);\r\n    font-size: 13px;\r\n}\r\n\r\n.contact-detail strong {\r\n    color: var(--theme-text-primary);\r\n}\r\n\r\n.modal-card-actions {\r\n    display: flex;\r\n    justify-content: flex-end;\r\n    gap: 10px;\r\n}\r\n\r\n/* Badges */\r\n.badge {\r\n    padding: 4px 12px;\r\n    border-radius: 999px;\r\n    font-size: 0.72rem;\r\n    font-weight: 700;\r\n    text-transform: uppercase;\r\n    letter-spacing: 0.5px;\r\n}\r\n\r\n.badge-success {\r\n    background: var(--success);\r\n    color: var(--theme-on-success);\r\n}\r\n\r\n.badge-warning {\r\n    background: var(--warning);\r\n    color: var(--theme-on-warning);\r\n}\r\n\r\n.badge-danger {\r\n    background: var(--danger);\r\n    color: var(--theme-on-danger);\r\n}\r\n\r\n.badge-primary {\r\n    background: var(--theme-primary-color);\r\n    color: var(--theme-highlight-light);\r\n}\r\n\r\n.badge-accent {\r\n    background: var(--theme-accent-bg-soft);\r\n    color: var(--theme-accent-color);\r\n    border: 1px solid var(--theme-accent-border);\r\n}\r\n\r\n/* Tables */\r\n.payments-table table,\r\n.clients-table table {\r\n    width: 100%;\r\n    border-collapse: collapse;\r\n}\r\n\r\n.payments-table th,\r\n.payments-table td,\r\n.clients-table th,\r\n.clients-table td {\r\n    padding: 14px 12px;\r\n    text-align: left;\r\n    border-bottom: 1px solid var(--theme-surface-border);\r\n}\r\n\r\n.payments-table th,\r\n.clients-table th {\r\n    font-weight: 700;\r\n    color: var(--theme-text-primary);\r\n    background: var(--theme-navlink-active-bg);\r\n    font-size: 0.9rem;\r\n}\r\n\r\n.payments-table td,\r\n.clients-table td {\r\n    color: var(--theme-text-secondary);\r\n    font-size: 0.95rem;\r\n}\r\n\r\n.client-cell {\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 12px;\r\n}\r\n\r\n.client-avatar {\r\n    width: 40px;\r\n    height: 40px;\r\n    border-radius: 50%;\r\n    background: linear-gradient(135deg, var(--theme-primary-color), var(--theme-accent-color));\r\n    color: var(--white);\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    font-weight: 800;\r\n    font-size: 0.9rem;\r\n    flex-shrink: 0;\r\n}\r\n\r\n.client-name {\r\n    font-weight: 700;\r\n    color: var(--theme-text-primary);\r\n}\r\n\r\n.client-email {\r\n    font-size: 0.85rem;\r\n    color: var(--theme-text-secondary);\r\n}\r\n\r\n/* Sidebar */\r\n.dashboard-sidebar .dashboard-card {\r\n    position: sticky;\r\n    top: calc(var(--nav-height) + 14px);\r\n}\r\n\r\n.quick-actions {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: var(--dashboard-quick-actions-gap);\r\n}\r\n\r\n.agent-info {\r\n    text-align: center;\r\n}\r\n\r\n.agent-avatar {\r\n    width: 80px;\r\n    height: 80px;\r\n    border-radius: 50%;\r\n    background: linear-gradient(135deg, var(--theme-primary-color), var(--theme-accent-color));\r\n    color: var(--white);\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    font-family: 'Cinzel', serif;\r\n    font-size: 2rem;\r\n    font-weight: 800;\r\n    margin: 0 auto 16px;\r\n    box-shadow: 0 10px 26px var(--theme-primary-shadow-color);\r\n}\r\n\r\n.agent-info h4 {\r\n    font-family: 'Cinzel', serif;\r\n    font-size: 1.15rem;\r\n    margin: 0 0 6px 0;\r\n    color: var(--theme-text-primary);\r\n}\r\n\r\n.agent-info p {\r\n    color: var(--theme-text-secondary);\r\n    font-size: 0.92rem;\r\n    margin: 0 0 14px 0;\r\n}\r\n\r\n.agent-contact {\r\n    margin: 14px 0;\r\n    padding: 14px;\r\n    background: var(--theme-navlink-active-bg);\r\n    border: 1px solid var(--theme-surface-border);\r\n    border-radius: 12px;\r\n    text-align: left;\r\n}\r\n\r\n.agent-contact p {\r\n    margin: 6px 0;\r\n    font-size: 0.92rem;\r\n    color: var(--theme-text-secondary);\r\n}\r\n\r\n/* Shared stat grid + cards */\r\n.dashboard-stats {\r\n    display: grid;\r\n    grid-template-columns: repeat(auto-fit, minmax(var(--dashboard-stat-min), 1fr));\r\n    gap: var(--dashboard-stat-gap, var(--dashboard-stat-gap-shared));\r\n    margin-top: 0;\r\n    margin-bottom: 12px;\r\n}\r\n\r\n.dashboard-stat-card {\r\n    background: linear-gradient(160