id rgba(0, 0, 0, 0.08);\r\n    border: 1px solid var(--dashboard-border, rgba(0, 0, 0, 0.06));\r\n    border-radius: 10px;\r\n    background: rgba(255, 255, 255, 0.86);\r\n    transition: all 0.2s ease;\r\n    box-shadow: 0 6px 14px rgba(0, 0, 0, 0.06);\r\n}\r\n\r\n.agent-dashboard .roadmap-step:first-child {\r\n    padding-top: 6px;\r\n}\r\n\r\n.agent-dashboard .roadmap-step:last-child {\r\n    border-left-color: transparent;\r\n    padding-bottom: 6px;\r\n}\r\n\r\n.agent-dashboard .roadmap-step::before {\r\n    content: '';\r\n    position: absolute;\r\n    left: 12px;\r\n    top: 16px;\r\n    width: 10px;\r\n    height: 10px;\r\n    border-radius: 50%;\r\n    background: rgba(0, 0, 0, 0.15);\r\n    border: 2px solid white;\r\n    transition: all 0.2s ease;\r\n}\r\n\r\n.agent-dashboard .roadmap-step.done {\r\n    border-left-color: #38ef7d;\r\n    border-color: rgba(56, 239, 125, 0.35);\r\n}\r\n\r\n.agent-dashboard .roadmap-step.done::before {\r\n    background: #38ef7d;\r\n    box-shadow: 0 0 0 4px rgba(56, 239, 125, 0.15);\r\n}\r\n\r\n.agent-dashboard .roadmap-step.in-progress {\r\n    border-left-color: #feca57;\r\n    background: rgba(254, 202, 87, 0.04);\r\n    border-color: rgba(254, 202, 87, 0.35);\r\n}\r\n\r\n.agent-dashboard .roadmap-step.in-progress::before {\r\n    background: #feca57;\r\n    box-shadow: 0 0 0 4px rgba(254, 202, 87, 0.15);\r\n    animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;\r\n}\r\n\r\n.agent-dashboard .roadmap-step.pending {\r\n    opacity: 0.6;\r\n}\r\n\r\n.agent-dashboard .roadmap-step .step-label {\r\n    display: block;\r\n    font-size: 0.85rem;\r\n    font-weight: 700;\r\n    color: var(--theme-text-primary);\r\n    margin-bottom: 2px;\r\n}\r\n\r\n.agent-dashboard .roadmap-step .step-meta {\r\n    display: block;\r\n    font-size: 0.7rem;\r\n    color: var(--theme-text-secondary);\r\n    font-weight: 500;\r\n}\r\n\r\n.agent-dashboard .roadmap-step .step-action {\r\n    position: absolute;\r\n    right: 10px;\r\n    top: 10px;\r\n    padding: 4px 8px;\r\n    border-radius: 8px;\r\n    border: 1px solid var(--theme-accent-border);\r\n    background: rgba(79, 172, 254, 0.12);\r\n    color: var(--theme-accent-color);\r\n    font-size: 0.7rem;\r\n    font-weight: 700;\r\n    cursor: pointer;\r\n    transition: all 0.2s ease;\r\n}\r\n\r\n.agent-dashboard .roadmap-step .step-action:hover {\r\n    background: rgba(79, 172, 254, 0.18);\r\n    border-color: var(--theme-accent-color);\r\n}\r\n\r\n/* Roadmap + steps en modo oscuro: superficies ms profundas y contrastadas */\r\nhtml[data-theme=\"dark-forest\"] .agent-dashboard .roadmap-body {\r\n    background: rgba(10, 20, 17, 0.88);\r\n    border: 1px solid rgba(76, 201, 191, 0.22);\r\n    box-shadow: 0 14px 30px rgba(0, 0, 0, 0.34);\r\n    color: var(--theme-text-primary);\r\n}\r\n\r\nhtml[data-theme=\"dark-forest\"] .agent-dashboard .roadmap-tab {\r\n    background: rgba(12, 24, 20, 0.9);\r\n    border: 1px solid rgba(76, 201, 191, 0.22);\r\n    color: var(--theme-text-primary);\r\n}\r\n\r\nhtml[data-theme=\"dark-forest\"] .agent-dashboard .roadmap-tab:hover {\r\n    background: rgba(76, 201, 191, 0.14);\r\n    border-color: rgba(76, 201, 191, 0.38);\r\n}\r\n\r\nhtml[data-theme=\"dark-forest\"] .agent-dashboard .roadmap-tab.active {\r\n    background: linear-gradient(135deg, rgba(76, 201, 191, 0.28), rgba(155, 89, 182, 0.24));\r\n    border-color: rgba(76, 201, 191, 0.42);\r\n    color: var(--theme-text-primary);\r\n}\r\n\r\nhtml[data-theme=\"dark-forest\"] .agent-dashboard .roadmap-step {\r\n    background: rgba(8, 16, 14, 0.9);\r\n    border: 1px solid rgba(76, 201, 191, 0.16);\r\n    border-left-color: rgba(76, 201, 191, 0.3);\r\n    box-shadow: 0 10px 22px rgba(0, 0, 0, 0.28);\r\n}\r\n\r\nhtml[data-theme=\"dark-forest\"] .agent-dashboard .roadmap-step::before {\r\n    background: rgba(76, 201, 191, 0.8);\r\n    border: 2px solid rgba(0, 0, 0, 0.35);\r\n    box-shadow: 0 0 0 4px rgba(76, 201, 191, 0.12);\r\n}\r\n\r\nhtml[data-theme=