.admin-dashboard {
    min-height: calc(100vh - var(--nav-height));
    color: var(--theme-text-primary);
    padding: calc(var(--nav-height) - var(--dashboard-padding-top-offset)) var(--dashboard-padding-x) var(--dashboard-padding-bottom);
    /* Role-specific stat sizing overrides (shared base in dashboards.css) */
    --dashboard-stat-gap: 16px;
    --dashboard-stat-icon-size: 64px;
    --dashboard-stat-icon-radius: 16px;
    --dashboard-stat-icon-font: 2.4rem;
    --dashboard-stat-shadow: 0 10px 36px rgba(0, 0, 0, 0.16), inset 0 1px 0 rgba(255, 255, 255, 0.16);
    --dashboard-stat-hover-shadow: 0 12px 34px rgba(0, 0, 0, 0.24), inset 0 1px 0 rgba(255, 255, 255, 0.26);
}

.admin-dashboard .dashboard-container {
    width: 100%;
    max-width: var(--dashboard-container-max-admin, var(--dashboard-container-max));
    margin: 0 auto;
    padding: 0 var(--dashboard-padding-x);
}

/* Admin Stats Grid - Larger */
.admin-dashboard .dashboard-stats {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(var(--dashboard-stat-min), 1fr));
    gap: var(--dashboard-stat-gap);
    margin-top: 0;
    margin-bottom: 12px;
}

.admin-dashboard .dashboard-stat-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
    transition: left 0.5s ease;
}

.admin-dashboard .dashboard-stat-card:hover::before {
    left: 100%;
}

.admin-dashboard .stat-label {
    font-size: var(--dashboard-text-md);
    color: var(--theme-text-secondary);
    font-weight: var(--dashboard-font-medium);
}`, "",{"version":3,"sources":["webpack://./styles/dashboards/admin-dashboard.css"],"names":[],"mappings":"AAAA,2BAA2B;AAC3B;IACI,2CAA2C;IAC3C,gCAAgC;IAChC,iIAAiI;IACjI,wEAAwE;IACxE,0BAA0B;IAC1B,gCAAgC;IAChC,kCAAkC;IAClC,kCAAkC;IAClC,iGAAiG;IACjG,uGAAuG;AAC3G;;AAEA;IACI,WAAW;IACX,+EAA+E;IAC/E,cAAc;IACd,qCAAqC;AACzC;;AAEA,8BAA8B;AAC9B;IACI,aAAa;IACb,+EAA+E;IAC/E,8BAA8B;IAC9B,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,WAAW;IACX,kBAAkB;IAClB,MAAM;IACN,WAAW;IACX,WAAW;IACX,YAAY;IACZ,sFAAsF;IACtF,0BAA0B;AAC9B;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,mCAAmC;IACnC,kCAAkC;IAClC,yCAAyC;AAC7C","sourcesContent":["/* Admin Dashboard Styles */\r\n.admin-dashboard {\r\n    min-height: calc(100vh - var(--nav-height));\r\n    color: var(--theme-text-primary);\r\n    padding: calc(var(--nav-height) - var(--dashboard-padding-top-offset)) var(--dashboard-padding-x) var(--dashboard-padding-bottom);\r\n    /* Role-specific stat sizing overrides (shared base in dashboards.css) */\r\n    --dashboard-stat-gap: 16px;\r\n    --dashboard-stat-icon-size: 64px;\r\n    --dashboard-stat-icon-radius: 16px;\r\n    --dashboard-stat-icon-font: 2.4rem;\r\n    --dashboard-stat-shadow: 0 10px 36px rgba(0, 0, 0, 0.16), inset 0 1px 0 rgba(255, 255, 255, 0.16);\r\n    --dashboard-stat-hover-shadow: 0 12px 34px rgba(0, 0, 0, 0.24), inset 0 1px 0 rgba(255, 255, 255, 0.26);\r\n}\r\n\r\n.admin-dashboard .dashboard-container {\r\n    width: 100%;\r\n    max-width: var(--dashboard-container-max-admin, var(--dashboard-container-max));\r\n    margin: 0 auto;\r\n    padding: 0 var(--dashboard-padding-x);\r\n}\r\n\r\n/* Admin Stats Grid - Larger */\r\n.admin-dashboard .dashboard-stats {\r\n    display: grid;\r\n    grid-template-columns: repeat(auto-fit, minmax(var(--dashboard-stat-min), 1fr));\r\n    gap: var(--dashboard-stat-gap);\r\n    margin-top: 0;\r\n    margin-bottom: 12px;\r\n}\r\n\r\n.admin-dashboard .dashboard-stat-card::before {\r\n    content: '';\r\n    position: absolute;\r\n    top: 0;\r\n    left: -100%;\r\n    width: