 * Estilos para scroll universal y modales
 * ESTRATEGIA: Un solo scroll en .dashboard-layout
 * dashboard-section es contenedor fijo, dashboard-layout tiene el scroll
 */

/* ========== SCROLL UNIVERSAL ========== */

/**
 * CONTENEDOR PRINCIPAL - FIJO SIN SCROLL
 * dashboard-section contiene todo pero NO tiene scroll
 */
.dashboard-section {
    position: fixed;
    inset: 0;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    /* Sin scroll en dashboard-section */
    display: flex;
    flex-direction: column;
}

/**
 * LAYOUT - NICO CON SCROLL
 * Este es el NICO elemento que tiene overflow: auto
 * Todo el contenido (sidebar, main, rail) se mueve junto
 */
.dashboard-section .dashboard-layout {
    flex: 1;
    display: grid;
    grid-template-columns: var(--sidebar-width) minmax(0, 1fr) var(--rail-width);
    grid-template-rows: 1fr;
    gap: 0;
    width: 100%;
    overflow-y: auto;
    /*  NICO SCROLL AQU */
    overflow-x: hidden;
    scroll-behavior: smooth;
    -webkit-overflow-scrolling: touch;
}

/**
 * COLUMNAS - Sin scroll individual, altura automtica
 * Cada columna crece segn su contenido
 */
.dashboard-section .dashboard-sidebar,
.dashboard-section .dashboard-main-wrapper,
.dashboard-section .dashboard-rail {
    overflow: visible;
    /* SIN scroll individual */
    height: auto;
    /* Altura segn contenido */
    min-height: 100vh;
    /* Mnimo altura de viewport */
}

/**
 * SUPERFICIES INTERNAS - Sin scroll, se expanden naturalmente
 */
.dashboard-section .sidebar-surface,
.dashboard-section .dashboard-stage,
.dashboard-section .rail-content {
    height: auto;
    overflow: visible;
}

/**
 * SIDEBAR STACK - Crece con contenido
 */
.dashboard-section .sidebar-stack {
    display: flex;
    flex-direction: column;
    gap: 12px;
    overflow: visible;
}

/**
 * CASOS ESPECIALES - Widgets con lmite de altura
 * Solo estos tienen scroll interno si exceden max-height
 */
.sidebar-widget-group,
.recent-clients-pills,
.rail-activity-list {
    max-height: 400px;
    overflow-y: auto;
    overflow-x: hidden;
}

/* Scrollbar para widgets limitados */
.sidebar-widget-group::-webkit-scrollbar,
.recent-clients-pills::-webkit-scrollbar,
.rail-activity-list::-webkit-scrollbar {
    width: 6px;
}

.sidebar-widget-group::-webkit-scrollbar-track,
.recent-clients-pills::-webkit-scrollbar-track,
.rail-activity-list::-webkit-scrollbar-track {
    background: transparent;
}

.sidebar-widget-group::-webkit-scrollbar-thumb,
.recent-clients-pills::-webkit-scrollbar-thumb,
.rail-activity-list::-webkit-scrollbar-thumb {
    background: rgba(0, 0, 0, 0.15);
    border-radius: 3px;
}

body.dark-forest .sidebar-widget-group::-webkit-scrollbar-thumb,
body.dark-forest .recent-clients-pills::-webkit-scrollbar-thumb,
body.dark-forest .rail-activity-list::-webkit-scrollbar-thumb,
body.dark-forest .sidebar-widget-group::-webkit-scrollbar-thumb,
body.dark-forest .recent-clients-pills::-webkit-scrollbar-thumb,
body.dark-forest .rail-activity-list::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.15);
}

/**
 * SCROLLBAR PRINCIPAL - Dashboard Layout
 * Scrollbar estilizada para el scroll universal en dashboard-layout
 */
.dashboard-section .dashboard-layout::-webkit-scrollbar {
    width: 10px;
}

.dashboard-section .dashboard-layout::-webkit-scrollbar-track {
    background: transparent;
}

.dashboard-section .dashboard-layout::-webkit-scrollbar-thumb {
    background: rgba(0, 0, 0, 0.25);
    border-radius: 5px;
    border: 2px solid transparent;
    background-clip: padding-box;
}

.dashboard-section .dashboard-layout::-webkit-scrollbar-thumb:hover {
    background: rgba(0, 0, 0, 0.35);
    background-clip: padding-box;
}

/* Dark mode scrollbar principal */
body.dark-forest .dashboard-section .dashboard-layout::-webkit-scrollbar-thumb,
body.dark-forest .dashboard-section .dashboard-layout::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.25);
    background-clip: padding-box;
}

body.dark-forest .dashboard-section .dashboard-layout::-webkit-scrollbar-thumb:hover,
body.dark-forest .dashboard-section .dashboard-layout::-webkit-scrollbar-thumb:hover {
    background: rgba(255, 255, 255, 0.35);
    background-clip: padding-box;
}

/**
 * CONTENT PADDING - Espacio al final para mejor UX
 */
.dashboard-section .dashboard-layout::after {
    content: '';
    display: block;
    height: 40px;
    grid-column: 1 / -1;
}

/**
 * SCROLL PADDING - Para anchor links y smooth scroll
 */
.dashboard-section {
    scroll-padding-top: 20px;
    scroll-padding-bottom: 20px;
}

.content-card,
.chart-card,
.stat-card {
    scroll-margin-top: 20px;
}

/* ========== MODAL STANDARDIZATION ========== */
/* NOTA: Los estilos de modales se han movido a modals-unified.css
 * Este archivo ahora solo contiene estilos de scroll especficos
 */

/* ========== DASHBOARD SCROLL FIXES ========== */

.client-dashboard,
.agent-dashboard,
.admin-dashboard {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

/* Hacer que stats-grid y dems elementos no afecten el scroll */
.stats-grid,
.mimic-wrap,
.dashboard-body {
    flex-shrink: 0;
    overflow: visible;
}

/* Asegurar que el contenido principal tenga espacio suficiente */
.dashboard-section .dashboard-main-wrapper {
    padding-bottom: 40px;
    /* Espacio al final para evitar cortar contenido */
}

/* Suavizar transiciones de scroll */
.dashboard-main-wrapper,
.sidebar-surface,
.dashboard-rail {
    scroll-padding: 20px;
}

/* Hacer que las tarjetas de contenido se comporten bien con scroll */
.content-card,
.chart-card,
.stat-card {
    scroll-margin-top: 20px;
}`, "",{"version":3,"sources":["webpack://./styles/scroll-modal-fixes.css"],"names":[],"mappings":"AAAA;;;;EAIE;;AAEF,2CAA2C;;AAE3C;;;EAGE;AACF;IACI,eAAe;IACf,QAAQ;IACR,WAAW;IACX,aAAa;IACb,gBAAgB;IAChB,oCAAoC;IACpC,aAAa;IACb,sBAAsB;AAC1B;;AAEA;;;;EAIE;AACF;IACI,OAAO;IACP,aAAa;IACb,4EAA4E;IAC5E,uBAAuB;IACvB,MAAM;IACN,WAAW;IACX,gBAAgB;IAChB,wBAAwB;IACxB,kBAAkB;IAClB,uBAAuB;IACvB,iCAAiC;AACrC;;AAEA;;;EAGE;AACF;;;IAGI,iBAAiB;IACjB,0BAA0B;IAC1B,YAAY;IACZ,2BAA2B;IAC3B,iBAAiB;IACjB,8BAA8B;AAClC;;AAEA;;EAEE;AACF;;;IAGI,YAAY;IACZ,iBAAiB;AACrB;;AAEA;;EAEE;AACF;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,iBAAiB;AACrB;;AAEA;;;EAGE;AACF;;;IAGI,iBAAiB;IACjB,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA,qCAAqC;AACrC;;;IAGI,UAAU;AACd;;AAEA;;;IAGI,uBAAuB;AAC3B;;AAEA;;;IAGI,+BAA+B;IAC/B,kBAAkB;AACtB;;AAEA;;;;;;IAMI,qCAAqC;AACzC;;AAEA;;;EAGE;AACF;IACI,WAAW;AACf;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,+BAA+B;IAC/B,kBAAkB;IAClB,6BAA6B;IAC7B,4BAA4B;AAChC;;AAEA;IACI,+BAA+B;IAC/B,4BAA4B;AAChC;;AAEA,kCAAkC;AAClC;;IAEI,qCAAqC;IACrC,4BAA4B;AAChC;;AAEA;;IAEI,qCAAqC;IACrC,4BAA4B;AAChC;;AAEA;;EAEE;AACF;IACI,WAAW;IACX,cAAc;IACd,YAAY;IACZ,mBAAmB;AACvB;;AAEA;;EAEE;AACF;IACI,wBAAwB;IACxB,2BAA2B;AAC/B;;AAEA;;;IAGI,uBAAuB;AAC3B;;AAEA,gDAAgD;AAChD;;EAEE;;AAEF,iDAAiD;;AAEjD;;;IAGI,kBAAkB;IAClB,WAAW;IACX,aAAa;IACb,gBAAgB;IAChB,aAAa;IACb,sBAAsB;AAC1B;;AAEA,gEAAgE;AAChE;;;IAGI,cAAc;IACd,iBAAiB;AACrB;;AAEA,iEAAiE;AACjE;IACI,oBAAoB;IACpB,kDAAkD;AACtD;;AAEA,oCAAoC;AACpC;;;IAGI,oBAAoB;AACxB;;AAEA,qEAAqE;AACrE;;;IAGI,uBAAuB;AAC3B","sourcesContent":["/**\r\n * Estilos para scroll universal y modales\r\n * ESTRATEGIA: Un solo scroll en .dashboard-layout\r\n * dashboard-section es contenedor fijo, dashboard-layout tiene el scroll\r\n */\r\n\r\n/* ========== SCROLL UNIVERSAL ========== */\r\n\r\n/**\r\n * CONTENEDOR PRINCIPAL - FIJO SIN SCROLL\r\n * dashboard-section contiene todo pero NO tiene scroll\r\n */\r\n.dashboard-section {\r\n    position: fixed;\r\n    inset: 0;\r\n    width: 100%;\r\n    height: 100vh;\r\n    overflow: hidden;\r