  - Aadir el atributo \`data-theme="dark-forest"\` al elemento <html> o <body>
  - O desde JS: document.documentElement.setAttribute('data-theme','dark-forest')
*/

/* Global animated background ("canvas") should match Services/About palette */
html[data-theme="dark-forest"] body {
    background-image:
        linear-gradient(135deg, var(--brand-purple) 0%, var(--brand-maroon) 50%, var(--wine) 100%),
        radial-gradient(circle at 20% 50%, var(--theme-highlight-glow-subtle) 0%, transparent 50%),
        radial-gradient(circle at 80% 80%, var(--theme-highlight-strong-border) 0%, transparent 55%);
    background-size: 300% 300%;
    background-attachment: fixed !important;
    animation: gradientShift 20s ease infinite;
    color: #e8f4ef !important;
}

/* Force light text color on all text elements in dark theme */
html[data-theme="dark-forest"] p,
html[data-theme="dark-forest"] span,
html[data-theme="dark-forest"] div,
html[data-theme="dark-forest"] h1,
html[data-theme="dark-forest"] h2,
html[data-theme="dark-forest"] h3,
html[data-theme="dark-forest"] h4,
