    --gray-200: #0b241a;
    --gray-300: #082015;

    --success: #28a745;
    --warning: #ffc107;
    --danger: #dc3545;
    --info: #17a2b8;

    /* Gradient tokens (keep in sync with Services/About backgrounds) */
    --gradient-start: #0f2b24;
    --gradient-mid: #1b3f47;
    --gradient-end: #2f6b7a;

    /* Sombras y borders */
    --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.58);
    --shadow: 0 4px 16px rgba(0, 0, 0, 0.62);
    --shadow-lg: 0 8px 32px rgba(0, 0, 0, 0.68);

