urate(var(--acrylic-saturate));\r\n    box-shadow: var(--acrylic-shadow);\r\n}\r\n\r\nhtml[data-theme=\"dark-forest\"] .navbar,\r\nhtml[data-theme=\"dark-forest\"] .sub-navigation {\r\n    background: rgba(6, 31, 16, 0.38) !important;\r\n    border-bottom: 1px solid var(--theme-surface-border-strong);\r\n    backdrop-filter: blur(var(--acrylic-blur-medium)) saturate(var(--acrylic-saturate)) !important;\r\n    -webkit-backdrop-filter: blur(var(--acrylic-blur-medium)) saturate(var(--acrylic-saturate)) !important;\r\n}\r\n\r\nhtml[data-theme=\"dark-forest\"] .btn-primary,\r\nhtml[data-theme=\"dark-forest\"] .btn-secondary,\r\nhtml[data-theme=\"dark-forest\"] .btn-outline {\r\n    background: radial-gradient(circle at 35% 35%, var(--theme-primary-color) 0%, var(--theme-secondary-color) 55%, var(--theme-contrast-color) 115%);\r\n    color: var(--theme-sunlight);\r\n    border: 1px solid var(--theme-contrast-glow);\r\n    backdrop-filter: blur(var(--acrylic-blur-light)) saturate(var(--acrylic-saturate));\r\n    -webkit-backdrop-filter: blur(var(--acrylic-blur-light)) saturate(var(--acrylic-saturate));\r\n    box-shadow: 0 10px 26px rgba(0, 0, 0, 0.5), 0 4px 14px var(--theme-contrast-shadow);\r\n}\r\n\r\nhtml[data-theme=\"dark-forest\"] .btn-primary:hover,\r\nhtml[data-theme=\"dark-forest\"] .btn-secondary:hover,\r\nhtml[data-theme=\"dark-forest\"] .btn-outline:hover {\r\n    color: var(--theme-sunlight-strong);\r\n    border-color: var(--theme-highlight-border);\r\n    box-shadow: 0 12px 32px rgba(0, 0, 0, 0.6), 0 6px 18px var(--theme-contrast-shadow);\r\n}\r\n\r\nhtml[data-theme=\"dark-forest\"] .auth-card .btn-primary {\r\n    background: radial-gradient(circle at 35% 35%, var(--theme-primary-color) 0%, var(--theme-secondary-color) 85%);\r\n    color: var(--theme-text-primary);\r\n    border: 1px solid rgba(232, 246, 238, 0.18);\r\n    box-shadow: 0 10px 24px rgba(0, 0, 0, 0.28);\r\n}\r\n\r\nhtml[data-theme=\"dark-forest\"] .auth-card .btn-primary:hover {\r\n    filter: