___CSS_LOADER_EXPORT___.push([module.id, `/* ===================================
   DIRECT MESSAGES & GLOBAL SEARCH STYLES
   =================================== */

/* Badge contador en header */
.badge-count {
    position: absolute;
    top: -4px;
    right: -4px;
    background: #f44336;
    color: white;
    border-radius: 10px;
    padding: 2px 6px;
    font-size: 0.6875rem;
    font-weight: 600;
    min-width: 18px;
    text-align: center;
    line-height: 1.2;
}

.icon-pill {
    position: relative;
}

/* Direct Messages Modal */
.dm-modal-container {
    max-width: 900px;
    width: 90vw;
    max-height: 80vh;
    display: flex;
    flex-direction: column;
}

.dm-content {
    display: grid;
    grid-template-columns: 300px 1fr;
    gap: 0;
    flex: 1;
    overflow: hidden;
}

.dm-threads-list {
    border-right: 1px solid var(--border-color);
    overflow-y: auto;
    background: var(--card-background);
}

.dm-thread-item {
    display: flex;
    gap: 12px;
    padding: 12px;
    cursor: pointer;
    transition: background 0.2s ease;
    border-bottom: 1px solid var(--border-color);
}

.dm-thread-item:hover {
    background: var(--hover-background);
}

.dm-thread-item.unread {
    background: rgba(var(--primary-rgb), 0.05);
}

.dm-thread-avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: var(--primary-color);
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
}

.dm-thread-info {
    flex: 1;
    min-width: 0;
}

.dm-thread-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 4px;
}

.dm-thread-header strong {
    font-size: 0.9375rem;
    color: var(--text-color);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.dm-thread-time {
    font-size: 0.75rem;
    color: var(--text-muted);
}

.dm-thread-preview {
    font-size: 0.875rem;
    color: var(--text-muted);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-bottom: 4px;
}

.dm-thread-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.dm-expires {
    font-size: 0.75rem;
    color: #ff9800;
}

.dm-unread-badge {
    background: var(--primary-color);
    color: white;
    border-radius: 10px;
    padding: 2px 8px;
    font-size: 0.75rem;
    font-weight: 600;
}

.dm-messages-area {
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

.dm-empty-state,
.dm-empty-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 3rem 2rem;
    color: var(--text-muted);
    text-align: center;
    height: 100%;
}

.dm-empty-state svg,
.dm-empty-list svg {
    opacity: 0.3;
    margin-bottom: 1rem;
}

.dm-messages-header {
    padding: 16px;
    border-bottom: 1px solid var(--border-color);
    display: flex;
    align-items: center;
    gap: 12px;
    background: var(--card-background);
}

.btn-back {
    padding: 8px;
    background: transparent;
    border: 1px solid var(--border-color);
    border-radius: 4px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.btn-back:hover {
    background: var(--hover-background);
}

.dm-messages-list {
    flex: 1;
    overflow-y: auto;
    padding: 16px;
    background: var(--background-color);
}

.dm-message {
    margin-bottom: 16px;
    display: flex;
}

.dm-message.sent {
    justify-content: flex-end;
}

.dm-message.received {
    justify-content: flex-start;
}

.dm-message-content {
    max-width: 70%;
    background: var(--card-background);
    padding: 12px;
    border-radius: 12px;
}

.dm-message.sent .dm-message-content {
    background: var(--primary-color);
    color: white;
}

.dm-message-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 6px;
}

.dm-message-header strong {
    font-size: 0.875rem;
}

.dm-message-time {
    font-size: 0.75rem;
    opacity: 0.7;
    margin-left: 8px;
}

.dm-message-content p {
    margin: 0;
    font-size: 0.9375rem;
    line-height: 1.5;
    word-wrap: break-word;
}

.dm-message-input {
    padding: 16px;
    border-top: 1px solid var(--border-color);
    display: flex;
    gap: 12px;
    background: var(--card-background);
}

.dm-message-input textarea {
    flex: 1;
    padding: 12px;
    border: 1px solid var(--border-color);
    border-radius: 8px;
    resize: none;
    font-family: inherit;
    font-size: 0.9375rem;
}

.btn-send {
    padding: 12px 16px;
    background: var(--primary-color);
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.btn-send:hover {
    background: var(--primary-hover);
}

/* Contact Modal */
.contact-modal-container {
    max-width: 600px;
    max-height: 80vh;
    display: flex;
    flex-direction: column;
}

.contact-search-box {
    position: relative;
    margin-bottom: 1.5rem;
}

.contact-search-box svg {
    position: absolute;
    left: 12px;
    top: 50%;
    transform: translateY(-50%);
    color: var(--text-muted);
}

.contact-search-box input {
    width: 100%;
    padding: 12px 12px 12px 40px;
    border: 1px solid var(--border-color);
    border-radius: 8px;
    font-size: 0.9375rem;
}

.contact-section {
    margin-bottom: 1.5rem;
}

.contact-section-title {
    font-size: 0.875rem;
    font-weight: 600;
    color: var(--text-muted);
    text-transform: uppercase;
    margin-bottom: 0.75rem;
}

.contact-list {
    max-height: 300px;
    overflow-y: auto;
}

.contact-item {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 12px;
    border-radius: 8px;
    cursor: pointer;
    transition: background 0.2s ease;
    border-bottom: 1px solid var(--border-color);
}

.contact-item:hover {
    background: var(--hover-background);
}

.contact-avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: var(--primary-color);
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
    font-size: 0.875rem;
}

.contact-info {
    flex: 1;
    min-width: 0;
}

.contact-info strong {
    display: block;
    font-size: 0.9375rem;
    color: var(--text-color);
    margin-bottom: 2px;
}

.contact-info small {
    display: block;
    font-size: 0.8125rem;
    color: var(--text-muted);
}

.contact-actions {
    display: flex;
    gap: 8px;
}

.btn-action {
    padding: 8px;
    background: transparent;
    border: 1px solid var(--border-color);
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.2s ease;
    display: flex;
    align-items: center;
    justify-content: center;
}

.btn-action:hover {
    background: var(--hover-background);
    border-color: var(--primary-color);
}

.btn-action.btn-message:hover {
    background: var(--primary-color);
    color: white;
    border-color: var(--primary-color);
}

/* Info Popup */
.info-popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10001;
    opacity: 0;
    transition: opacity 0.3s ease;
}

.info-popup-overlay.active {
    opacity: 1;
}

.info-popup {
    background: var(--card-background);
    border-radius: 12px;
    padding: 24px;
    max-width: 400px;
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);
}

.client-info-popup h4 {
    margin: 0 0 16px 0;
    color: var(--text-color);
}

.info-row {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 12px;
    color: var(--text-color);
}

.info-row svg {
    color: var(--text-muted);
}

.info-row a {
    color: var(--primary-color);
    text-decoration: none;
}

.info-row a:hover {
    text-decoration: underline;
}

/* Global Search Results */
.search-results {
    position: absolute;
    top: calc(100% + 8px);
    left: 0;
    right: 0;
    background: var(--card-background);
    border: 1px solid var(--border-color);
    border-radius: 8px;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.15);
    max-height: 500px;
    overflow-y: auto;
    z-index: 1000;
}

.search-results-header {
    padding: 12px 16px;
    border-bottom: 1px solid var(--border-color);
    font-weight: 600;
    font-size: 0.875rem;
    color: var(--text-color);
}

.search-category {
    padding: 12px 0;
    border-bottom: 1px solid var(--border-color);
}

.search-category:last-child {
    border-bottom: none;
}

.search-category h4 {
    margin: 0 16px 8px 16px;
    font-size: 0.8125rem;
    font-weight: 600;
    color: var(--text-muted);
    text-transform: uppercase;
    display: flex;
    align-items: center;
    gap: 6px;
}

.search-result-item {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 10px 16px;
    cursor: pointer;
    transition: background 0.2s ease;
}

.search-result-item:hover {
    background: var(--hover-background);
}

.search-result-icon {
    width: 32px;
    height: 32px;
    border-radius: 6px;
    background: rgba(var(--primary-rgb), 0.1);
    color: var(--primary-color);
    display: flex;
    align-items: center;
    justify-content: center;
}

.search-result-item strong {
    display: block;
    font-size: 0.9375rem;
    color: var(--text-color);
    margin-bottom: 2px;
}

.search-result-item small {
    display: block;
    font-size: 0.8125rem;
    color: var(--text-muted);
}

.search-loading,
.search-no-results,
.search-error {
    padding: 3rem 2rem;
    text-align: center;
    color: var(--text-muted);
}

.search-loading .spinner {
    width: 40px;
    height: 40px;
    border: 3px solid var(--border-color);
    border-top-color: var(--primary-color);
    border-radius: 50%;
    animation: spin 0.8s linear infinite;
    margin: 0 auto 1rem auto;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

/* Dark Theme Adjustments */
body.dark-forest .dm-thread-item.unread {
    background: rgba(var(--primary-rgb), 0.1);
}

body.dark-forest .dm-message.sent .dm-message-content {
    background: var(--primary-color);
}

body.dark-forest .search-results {
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.4);
}

body.dark-forest .info-popup {
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);
}

/* Responsive */
@media (max-width: 768px) {
    .dm-content {
        grid-template-columns: 1fr;
    }

    .dm-threads-list {
        border-right: none;
    }

    .dm-message-content {
        max-width: 85%;
    }

    .contact-modal-container {
        width: 95vw;
    }
