___CSS_LOADER_EXPORT___.push([module.id, `/**
 * Calendar and Scheduling Styles
 * Integrates with existing theme color system
 */

/* Calendar Container */
.calendar-section {
    background: var(--theme-surface-bg);
    border-radius: 16px;
    padding: var(--dashboard-card-padding);
    border: 1px solid var(--theme-surface-border);
    box-shadow: var(--shadow-sm);
}

.calendar-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
    gap: 12px;
}

.calendar-header h2 {
    color: var(--theme-text-primary);
    font-size: 1.5rem;
    font-weight: 600;
    margin: 0;
}

.calendar-nav {
    display: flex;
    gap: 8px;
}

.calendar-nav button {
    width: 36px;
    height: 36px;
    border-radius: 8px;
    border: 1px solid var(--theme-accent-border);
    background: var(--theme-accent-bg-soft);
    color: var(--theme-primary-color);
    cursor: pointer;
    transition: all 0.2s ease;
    font-weight: 600;
}

.calendar-nav button:hover {
    background: var(--theme-accent-border);
    transform: translateY(-1px);
}

/* Month/Week View */
.calendar-grid {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 8px;
    margin-bottom: 24px;
}

.calendar-day {
    aspect-ratio: 1;
    padding: 12px;
    border-radius: 12px;
    border: 2px solid var(--theme-surface-border);
    background: var(--white);
    cursor: pointer;
    transition: all 0.2s ease;
    display: flex;
    flex-direction: column;
}

.calendar-day:hover:not(.disabled) {
    border-color: var(--theme-primary-color);
    background: var(--theme-accent-bg-soft);
    transform: translateY(-2px);
}

.calendar-day.today {
    background: linear-gradient(135deg, var(--theme-primary-color) 0%, var(--theme-accent-color) 100%);
    color: var(--white);
    border-color: var(--theme-primary-color);
    font-weight: 600;
}

.calendar-day.selected {
    background: var(--theme-accent-color);
    color: var(--white);
    border-color: var(--theme-accent-color);
}

.calendar-day.disabled {
    opacity: 0.4;
    cursor: not-allowed;
    background: var(--gray-100);
}

.calendar-day-number {
    font-size: 0.9rem;
    font-weight: 600;
    margin-bottom: 4px;
}

.calendar-day-events {
    font-size: 0.65rem;
    color: var(--theme-accent-color);
    overflow: hidden;
    max-height: 100%;
}

/* Time Slots */
.time-slots {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
    gap: 10px;
    margin-bottom: 24px;
}

.time-slot {
    padding: 12px 16px;
    border-radius: 10px;
    border: 2px solid var(--theme-surface-border);
    background: var(--white);
    cursor: pointer;
    text-align: center;
    font-size: 0.9rem;
    color: var(--theme-text-primary);
    transition: all 0.2s ease;
}

.time-slot:hover:not(.booked) {
    border-color: var(--theme-primary-color);
    background: var(--theme-accent-bg-soft);
    transform: translateY(-2px);
}

.time-slot.selected {
    background: var(--theme-primary-color);
    color: var(--white);
    border-color: var(--theme-primary-color);
    font-weight: 600;
}

.time-slot.booked {
    opacity: 0.5;
    cursor: not-allowed;
    background: var(--gray-100);
    border-color: var(--gray-300);
}

/* Meeting Card */
.meeting-card {
    padding: var(--dashboard-card-padding);
    border-radius: 12px;
    border: 1px solid var(--theme-accent-border);
    background: var(--theme-accent-bg-soft);
    margin-bottom: 12px;
    transition: all 0.2s ease;
}

.meeting-card:hover {
    border-color: var(--theme-accent-border-strong);
    box-shadow: var(--theme-accent-shadow-soft);
}

.meeting-card-header {
    display: flex;
    justify-content: space-between;
    align-items: start;
    margin-bottom: 12px;
}

.meeting-card-title {
    color: var(--theme-primary-color);
    font-weight: 600;
    font-size: 1rem;
    margin: 0;
}

.meeting-badge {
    display: inline-block;
    padding: 4px 10px;
    border-radius: 6px;
    font-size: 0.75rem;
    font-weight: 600;
    text-transform: uppercase;
}

.meeting-badge.confirmed {
    background: rgba(40, 167, 69, 0.15);
    color: #28a745;
    border: 1px solid rgba(40, 167, 69, 0.3);
}

.meeting-badge.requested {
    background: rgba(255, 193, 7, 0.15);
    color: #ffc107;
    border: 1px solid rgba(255, 193, 7, 0.3);
}

.meeting-badge.completed {
