    border-radius: 50%;
}

.status-dot.available {
    background: #28a745;
}

.status-dot.busy {
    background: #ffc107;
}

.status-dot.offline {
    background: #6c757d;
}

/* Responsive */
@media (max-width: 768px) {
    .calendar-grid {
        grid-template-columns: repeat(4, 1fr);
    }

    .time-slots {
        grid-template-columns: repeat(2, 1fr);
