qFAAqF;IACrF,4CAA4C;IAC5C,iCAAiC;IACjC,6CAA6C;AACjD;;AAEA;IACI,qFAAqF;IACrF,uCAAuC;IACvC,6CAA6C;AACjD","sourcesContent":["/**\r\n * Calendar and Scheduling Styles\r\n * Integrates with existing theme color system\r\n */\r\n\r\n/* Calendar Container */\r\n.calendar-section {\r\n    background: var(--theme-surface-bg);\r\n    border-radius: 16px;\r\n    padding: var(--dashboard-card-padding);\r\n    border: 1px solid var(--theme-surface-border);\r\n    box-shadow: var(--shadow-sm);\r\n}\r\n\r\n.calendar-header {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    margin-bottom: 24px;\r\n    gap: 12px;\r\n}\r\n\r\n.calendar-header h2 {\r\n    color: var(--theme-text-primary);\r\n    font-size: 1.5rem;\r\n    font-weight: 600;\r\n    margin: 0;\r\n}\r\n\r\n.calendar-nav {\r\n    display: flex;\r\n    gap: 8px;\r\n}\r\n\r\n.calendar-nav button {\r\n    width: 36px;\r\n    height: 36px;\r\n    border-radius: 8px;\r\n    border: 1px solid var(--theme-accent-border);\r\n    background: var(--theme-accent-bg-soft);\r\n    color: var(--theme-primary-color);\r\n    cursor: pointer;\r\n    transition: all 0.2s ease;\r\n    font-weight: 600;\r\n}\r\n\r\n.calendar-nav button:hover {\r\n    background: var(--theme-accent-border);\r\n    transform: translateY(-1px);\r\n}\r\n\r\n/* Month/Week View */\r\n.calendar-grid {\r\n    display: grid;\r\n    grid-template-columns: repeat(7, 1fr);\r\n    gap: 8px;\r\n    margin-bottom: 24px;\r\n}\r\n\r\n.calendar-day {\r\n    aspect-ratio: 1;\r\n    padding: 12px;\r\n    border-radius: 12px;\r\n    border: 2px solid var(--theme-surface-border);\r\n    background: var(--white);\r\n    cursor: pointer;\r\n    transition: all 0.2s ease;\r\n    display: flex;\r\n    flex-direction: column;\r\n}\r\n\r\n.calendar-day:hover:not(.disabled) {\r\n    border-color: var(--theme-primary-color);\r\n    background: var(--theme-accent-bg-soft);\r\n    transform: translateY(-2px);\r\n}\r\n\r\n.calendar-day.today {\r\n    background: linear-gradient(135deg, var(--theme-primary-color) 0%, var(--theme-accent-color) 100%);\r\n    color: var(--white);\r\n    border-color: var(--theme-primary-color);\r\n    font-weight: 600;\r\n}\r\n\r\n.calendar-day.selected {\r\n    background: var(--theme-accent-color);\r\n    color: var(--white);\r\n    border-color: var(--theme-accent-color);\r\n}\r\n\r\n.calendar-day.disabled {\r\n    opacity: 0.4;\r\n    cursor: not-allowed;\r\n    background: var(--gray-100);\r\n}\r\n\r\n.calendar-day-number {\r\n    font-size: 0.9rem;\r\n    font-weight: 600;\r\n    margin-bottom: 4px;\r\n}\r\n\r\n.calendar-day-events {\r\n    font-size: 0.65rem;\r\n    color: var(--theme-accent-color);\r\n    overflow: hidden;\r\n    max-height: 100%;\r\n}\r\n\r\n/* Time Slots */\r\n.time-slots {\r\n    display: grid;\r\n    grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));\r\n    gap: 10px;\r\n    margin-bottom: 24px;\r\n}\r\n\r\n.time-slot {\r\n    padding: 12px 16px;\r\n    border-radius: 10px;\r\n    border: 2px solid var(--theme-surface-border);\r\n    background: var(--white);\r\n    cursor: pointer;\r\n    text-align: center;\r\n    font-size: 0.9rem;\r\n    color: var(--theme-text-primary);\r\n    transition: all 0.2s ease;\r\n}\r\n\r\n.time-slot:hover:not(.booked) {\r\n    border-color: var(--theme-primary-color);\r\n    background: var(--theme-accent-bg-soft);\r\n    transform: translateY(-2px);\r\n}\r\n\r\n.time-slot.selected {\r\n    background: var(--theme-primary-color);\r\n    color: var(--white);\r\n    border-color: var(--theme-primary-color);\r\n    font-weight: 600;\r\n}\r\n\r\n.time-slot.booked {\r\n    opacity: 0.5;\r\n    cursor: not-allowed;\r\n    background: var(--gray-100);\r\n    border-color: var(--gray-300);\r\n}\r\n\r\n/* Meeting Card */\r\n.meeting-card {\r\n    padding: var(--dashboard-card-padding);\r\n    border-radius: 12px;\r\n    border: 1px solid var(--theme-accent-border);\r\n    background: var(--theme-accent-bg-soft);\r\n    margin-bottom: 12px;\r\n    transition: all 0.2s ease;\r\n}\r\n\r\n.meeting-card:hover {\r\n    border-color: var(--theme-accent-border-strong);\r\n    box-shadow: var(--theme-accent-shadow-soft);\r\n}\r\n\r\n.meeting-card-header {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: start;\r\n    margin-bottom: 12px;\r\n}\r\n\r\n.meeting-card-title {\r\n    color: var(--theme-primary-color);\r\n    font-weight: 600;\r\n    font-size: 1rem;\r\n    margin: 0;\r\n}\r\n\r\n.meeting-badge {\r\n    display: inline-block;\r\n    padding: 4px 10px;\r\n    border-radius: 6px;\r\n    font-size: 0.75rem;\r\n    font-weight: 600;\r\n    text-transform: uppercase;\r\n}\r\n\r\n.meeting-badge.confirmed {\r\n    background: rgba(40, 167, 69, 0.15);\r\n    color: #28a745;\r\n    border: 1px solid rgba(40, 167, 69, 0.3);\r\n}\r\n\r\n.meeting-badge.requested {\r\n    background: rgba(255, 193, 7, 0.15);\r\n    color: #ffc107;\r\n    border: 1px solid rgba(255, 193, 7, 0.3);\r\n}\r\n\r\n.meeting-badge.completed {\r\n    background: rgba(23, 162, 184, 0.15);\r\n    color: #17a2b8;\r\n    border: 1px solid rgba(23, 162, 184, 0.3);\r\n}\r\n\r\n.meeting-badge.cancelled {\r\n    background: rgba(220, 53, 69, 0.15);\r\n    color: #dc3545;\r\n    border: 1px solid rgba(220, 53, 69, 0.3);\r\n}\r\n\r\n.meeting-card-details {\r\n    color: var(--theme-text-secondary);\r\n    font-size: 0.9rem;\r\n    line-height: 1.6;\r\n    margin-bottom: 12px;\r\n}\r\n\r\n.meeting-card-details strong {\r\n    color: var(--theme-text-primary);\r\n}\r\n\r\n.meeting-card-actions {\r\n    display: flex;\r\n    gap: 8px;\r\n    flex-wrap: wrap;\r\n}\r\n\r\n.meeting-btn {\r\n    padding: 8px 14px;\r\n    border-radius: 8px;\r\n    border: 1px solid;\r\n    font-size: 0.85rem;\r\n    cursor: pointer;\r\n    transition: all 0.2s ease;\r\n    font-weight: 500;\r\n}\r\n\r\n.meeting-btn-primary {\r\n    background: var(--theme-primary-color);\r\n    color: var(--white);\r\n    border-color